<script>
import ProductsDropdown from './../../../../../components/Dropdowns/ProductsDropdown.svelte';
import Paginator from './../../../../../components/paginator/Paginator.svelte';
import { ApplicationData } from './../../../../../data.application.route.js';
import { page } from './../../../../../turpial.route.store';
import SearchInput from '../../../../../components/Inputs/searchInput.svelte';
import { CustomStore } from '../../../../../libraries/customStore/CustomStore.js';
import { FetchApp } from '../../../../../libraries/fetchApp';
import LoaderElement from '../../../../../components/Elements/LoaderElement.svelte';

let listData = [
    {
        appImage: "https://i.ibb.co/4NcJWrc/Logo-Efficommerce-H-FINAL-2x.png",
        description: "HOLDER FUNDA SOPORTE DE CELULAR PARA EL BAÑO RESISTENTE AL AGUA *N*",
        route: "projects/applications/effi",
        id: 1,
        ref: 1538,
        stock: 568
    },
    {
        appImage: "https://i.ibb.co/4NcJWrc/Logo-Efficommerce-H-FINAL-2x.png",
        description: "LAMPARA LED DE EMERGENCIA",
        options: "1",
        route: "projects/applications/effi",
        id: 1,
        ref: 1538,
        stock: 568
    },
    {
        appImage: "https://i.ibb.co/4NcJWrc/Logo-Efficommerce-H-FINAL-2x.png",
        description: "LAMPARA LED DE EMERGENCIA",
        options: "1",
        route: "projects/applications/effi",
        id: 1,
        ref: 1538,
        stock: 568
    },
]

let productsPaginator = new CustomStore(
    {
        currentPage: 1,
        limit: 0,
        url: `${$ApplicationData.api_host}public/api/v1/services/builder/products`,
        headers: {
            'X-Token-Website': $ApplicationData.token_website,
        },
        conditions: {
            search: "",
            perpage: 5,
            offset: 0,
            sortby: "asc",
            orderby: "created_at",
            fields: "title,description",
            from: "",
            until: "",
        },
    },
    function(){
        productsPaginator = productsPaginator;
    }
);

let list = [];
const list_application = new FetchApp(productsPaginator, (data)=>{
    productsPaginator.setJson("listData", data);
    list = data;
    productsPaginator.setJson("limit", Math.ceil(data.items / productsPaginator.data.conditions.perpage));
})

list_application.before_fetch = ()=>{
    productsPaginator.update(function(data){
        list = [];
        data.headers = {
            'X-Token-Website': $ApplicationData.token_website,
        }
        return data;
    });
}


let button_generating = "";
function generate(){
    button_generating = "is-loading"
    fetch(`${$ApplicationData.api_host}/public/api/v1/services/user/effi/tokens/${$ApplicationData.user.public_key}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
        method: "POST",
        body: {data: ""}
    })
    .then(( r )=>{
        return r.json();
    }).then(( data )=>{
        console.log(data);
        button_generating = ""
    })
}

function on_change(name, value, event){
    console.log(name, value, event);
}

console.log(productsPaginator)

$: {
    if($ApplicationData.website_selected){
        list_application.fetch();
    }
}


</script>
<style>
.product-img{
    max-width: 127px;
    width: 100%;
}
.box-container{
    background-color: var(--color-grey-background);
    border-radius: 20px;
    padding: 1.75rem;
}

.has-text-gray{
    color: #415B76;
}

.products-container{
    max-height: 400px;
    overflow-y: scroll;
}

</style>

{#if $ApplicationData.has_projects === true}
<section>
    <div>
        <img src="https://i.ibb.co/x3pFqdL/Logo-Efficommerce-H-FINAL.png" alt="effi-logo">
    </div>
    <hr>
    <div class="flex-center" style="gap: 0 20px;">
        <ProductsDropdown on_change={on_change}/>
        <SearchInput></SearchInput>
    </div>
    <div class="full-width flex-center-flex-end mt-3" style="padding: 0.7rem 0;">
        <div class="flex-center-space-between" style="width: 100%;">
            <div>
                <p class="fw600 has-text-gray">Productos (<span class="text has-text-weight-normal">Total</span> 2)</p>
            </div>
            <div>
                 <Paginator paginatorData={productsPaginator} list_application_fetch={list_application} />
            </div>
        </div>
        <div class="flex-center-space-between" style="min-width:fit-content;">
        </div>
    </div>
    <div class="products-container custom-scrollbar-gray">
        <ul class="overflow-container full-size">
            {#each listData as item}
                <li class="mt-1" style="border-radius: 20px;">
                    <section class="columns is-multiline box-container is-align-items-center mb-5" style="gap: 0 20px;">
                        <div class="column is-2" style="max-width: 127px;">
                            <img class="product-img" src={item.appImage} alt="logo">
                        </div>
                        <div class="column is-4">
                            <p class="fw600 has-text-gray">
                                {item.description}
                            </p>
                            <div class="mt-3 has-text-gray">
                                <span>Id: {item.id}</span>
                                <span>Ref: {item.ref}</span>
                                <span>Stock: {item.stock} unidades</span>
                            </div>
                        </div>
                        <div class="column has-text-gray">            
                            <p>Precio: $16.900</p>
                            <p>Precio de venta: 25.000</p>
                            <p>Ganancia: 47.93 %</p>
                        </div>                
                        <div class="column has-text-right">
                            <button class="button is-primary brand is-small is-rounded px-6 {button_generating}" on:click="{()=>{ generate()}}" type="button">Importar</button>
                        </div>
                    </section>
                </li>
            {/each}
        </ul>
    </div>
</section>
    {:else}
        <LoaderElement style_container="min-height:400px;"/>
{/if}