<script>
import IndexHeaderBox from './IndexHeaderBox.svelte';
import { ApplicationData } from '../../data.application.route';
import { CustomStore } from '../../libraries/customStore/CustomStore';
import LinkPrimary from './../../components/Buttons/LinkPrimary.svelte';

let dashboardData = new CustomStore(
    {
        order_counter: 0,
        order_value: 0,
        form_counter: 0,
        form_value: 0,
        from: "",
        until: "",
    },
    function(){
        dashboardData = dashboardData;
    }
);
function dashboard_fetch_all() {
    fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/orders?count=true&from=${dashboardData.data.from}&until=${dashboardData.data.until}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.text()).then((r)=>{
        dashboardData.setJson("order_counter", r);
    })
    fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/orders?sum=total&from=${dashboardData.data.from}&until=${dashboardData.data.until}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.text()).then((r)=>{
        dashboardData.setJson("order_value", r);
    })
    fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/forms?count=true&from=${dashboardData.data.from}&until=${dashboardData.data.until}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.text()).then((r)=>{
        dashboardData.setJson("form_counter", r);
    })
}
dashboard_fetch_all();

</script>
<style>
.info-container{
    width: 100%;
    margin-bottom: 2rem;
    height: fit-content;
    font-size: 14px;
}
.button{
    background: #3DCC14;
    border: none;
    color: white;
    font-size: 14px;
    border-radius: 46px;
}
.form-sales-container{
    width: 100%;
    height: 100%;
}
.form-sales-container .card{
    background: var(--color-grey-background);
    border: 2px solid #FFFFFF45;
    border-radius: 15px;
    height: 100%;
    width: 100%;
    padding: 0.75rem;
    color: #415B76;
    font-size: 14px;
}
.form-sales-container .card span{
    font-size: 18px;
}
.form-sales-container .card .is-line{
    width: 100%;
}
.graphics-container{
    margin-left: 1rem;
    width: calc(75% - 1rem);
    height: 100%;
}
.sales-chart, .forms-chart{
    width: 50%;
    height: 100%;
    border: solid green 1px;
}
@media only screen and (max-width: 1000px){
    .info-container{
        flex-direction: column;
        height: fit-content;
    }
    .info-container .form-sales-container{
        width: 90%;
        padding: 1rem;
        flex-direction: row;
    }
    .info-container .graphics-container{
        width: 100%;
        margin-left: 0;
        min-height: 250px;
        height: 300px;
    }
}
@media only screen and (max-width: 800px){
    .info-container .graphics-container{
        flex-direction: column;
        height: 550px;
    }
    .info-container .form-sales-container{
        width: 100%;
        padding: 1rem 0;
    }
    .info-container .graphics-container .sales-chart, .info-container .graphics-container .forms-chart{
        width: 100%;
        padding: 1rem;
    }
}

</style>
{#if $ApplicationData.has_projects === true}
    <section>
        <IndexHeaderBox dashboardData={dashboardData} dashboard_fetch_all={dashboard_fetch_all}></IndexHeaderBox>
        <div class="info-container mt-3 flex-center-space-between">
            <div class="form-sales-container flex-center-space-between full-size">
                <LinkPrimary type="linkContainer" path="projects/sales_report">
                    <div class="card flex-column-space-between">
                        <div class="is-line flex-center-space-between">
                            <span class="has-text-weight-medium">Ventas</span> <i class="icon-tripod-shopping-cart"></i>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>Ordenes: <span class="has-text-weight-medium">{dashboardData.data.order_counter}</span></p>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>valor: <span class="has-text-weight-medium">{`$${dashboardData.data.order_value} COP`}</span></p>
                        </div>
                        <div class="is-line flex-center-flex-end">
                            <p style="font-size: 12px; color: #296fff;">ver más</p>
                        </div>
                    </div>
                </LinkPrimary>
                <div class="is-line" style="padding: 0.5rem;"></div>
                <LinkPrimary type="linkContainer" path="projects/forms_report">
                    <div class="card flex-column-space-between">
                        <div class="is-line flex-center-space-between">
                            <span class="has-text-weight-medium">Formularios</span> <i class="icon-tripod-list-v2"></i>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>Ordenes: <span class="has-text-weight-medium">{dashboardData.data.form_counter}</span></p>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>valor: <span class="has-text-weight-medium">{`$${dashboardData.data.form_value} COP`}</span></p>
                        </div>
                        <div class="is-line flex-center-flex-end">
                            <p style="font-size: 12px; color: #296fff;">ver más</p>
                        </div>
                    </div>
                </LinkPrimary>
            </div>
            <!-- <div class="graphics-container flex-center">
                <div class="sales-chart">
                    chart 1
                </div>
                <div class="forms-chart">
                    chart 2
                </div>
            </div> -->
        </div>
        <div class="line flex-center">
            <button class="button brand" on:click="{function(e){ $ApplicationData.go_to_project() }}" style="padding: 24px 31px;">
                Gestionar proyectos
            </button>
        </div>
    </section>
{/if}
