<script>
import LoaderElement from './../../../../components/Elements/LoaderElement.svelte';
import Table from "../../../../components/Table/Table.svelte";
import { AppDate } from "../../../../libraries/coreApp";

export let paginatorData;

let checkList = [];

$:{
    if(paginatorData?.data){
        console.log("Ids seleccionados", checkList);
    }
}

function checkboxActive(id) {
    let element = document.getElementById(`${id}`);

    if(element.checked){
        checkList = [...checkList, element.id];
    }
    else{
        let checkExists = checkList.filter((check) => check !== element.id);
        checkList = checkExists;
    }
    paginatorData.setJson('orders_length_selected', checkList.length);
}

function updateAllChecks(data) {
    checkList = data;
    paginatorData.setJson('orders_length_selected', checkList.length);
}

</script>
<style>
.w-100{
    width: 100%;
}
.list-item-table{
    font-size:0.90rem;
    text-align: left;
    color: var(--color-grey);
    font-weight: 500;
} 
.link-item{
    font-size: 1rem;
    font-weight: 500;
    color: #296FFF;
}

</style>
<Table listHeader={['Checkbox', 'Orden','Fecha','Destinatario','Ubicación','Total', 'Estado', 'Aplicaciones']} paginatorData={paginatorData} updateAllChecks={updateAllChecks}>
    {#each paginatorData?.data?.listData?.data as data, i}
        <tr class="w-100">
            <td class="list-item-table">
                <input type="checkbox" class="input-checkbox" data-id={data.id} id={data.id} disabled={data.status === "pending" ? false : true} checked={data?.isChecked ? true : false}  on:click={() => checkboxActive(data.id)}>
            </td>
            <td class="list-item-table">{data.invoice_no}</td>
            <td class="list-item-table">{AppDate.onlydate(data.created_at, "date")}</td>
            <td class="list-item-table">
                <p style="overflow-wrap: anywhere;">
                    {data.customer_name}<br>
                    {data.customer_email}
                </p>
            </td>
            <td class="list-item-table">{data.shipping_address.country}</td>
            <td class="list-item-table">${data.total}</td>
            <td class="list-item-table" style="color: {data.statusHanddler?.color};"><div class="flex-center-flex-start"><i class="{data.statusHanddler?.icon}"></i> <p style="white-space: nowrap;">{data.statusHanddler?.text}</p></div></td>
            <td class="list-item-table">
                <a href="#path:/projects/sales_report/{data.invoice_no}">
                    Ver más detalles
                </a>
            </td>
        </tr>   
    {/each}
</Table>