<script>
import AppForms from "../../libraries/coreApp/AppForms"
export let paginatorData;
export let list_application_fetch;
let searchFor;

const search = () => {
    let data = AppForms.retrieve({
        form: searchFor,
        targets: ".formsElements"
    })
    paginatorData.update((object)=>{
        object.conditions.search = data.search;
        return object; 
    })

    console.log(paginatorData);

    list_application_fetch.fetch();
}

</script>
<div class="field w-100">
    <form action="POST" bind:this={searchFor} on:submit|preventDefault={() => search()}>
        <p class="control has-icons-right">
            <input type="search" class="input formsElements w-100" id="search" placeholder="Buscar" name="search">
            <span class="icon is-small is-right">
                <i class="icon-tripod-search is-size-5 has-text-grey mr-4" ></i>
            </span>
        </p>
    </form>
</div>
<style>
    .input{
        height: 42px;
        padding: .5rem 2.5rem;
        background: #F5F8FA;
        border: 1px solid #E1E8ED;
        box-shadow: none;
    }
    .input::placeholder{
        color: #B4C0C9;
    }
    .w-100{
        width: 100%;
    }
</style>