<script>
import LoaderElement from './../../../../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../../../../data.application.route.js';
import Sidebar from '.../../../components/Menus/Sidebar.svelte';
import MainHeader from '.../../../components/Headers/MainHeader.svelte';
import Main from '.../../../layouts/Main.svelte';
import CommonAppsLayout from '../components/CommonAppsLayout.svelte';
import HeaderBoxProjects from '../../HeaderBoxProjects.svelte';
import { CustomStore } from '../../../../libraries/customStore/CustomStore';
import { ApplicationInstaller } from './ApplicationInstaller';
import { ButtonStatus } from '../../../../libraries/coreApp';

let application_name = "effi";
let steps = [
    "Copia la url y los tokens de acceso.",
    "Dirígete a Efficommerce, ve a administración y luego a tienda virtual.",
    'Crea una nueva tienda y llena los datos que allí te solicitan.'
]
let logoUrl = "https://i.ibb.co/x3pFqdL/Logo-Efficommerce-H-FINAL.png";

let underLineText = "Importar productos";

/**
 * copier(): this function detects a click event on specific element and make a copi of elemnt content
 * @param e: is the event that was catched on element.
 */
function copier(e){
    let inputElement = e.target.previousElementSibling;
    inputElement.select(); 
    document.execCommand("copy"); 
}

let token_data = null;
fetch(`${$ApplicationData.api_host}/public/api/v1/services/user/effi/tokens/${$ApplicationData.user.public_key}`, {
    headers: {'X-Token-Website': $ApplicationData.token_website},
})
.then(( r )=>{
    return r.json();
}).then(( data )=>{
    token_data = data
})



let button_generating = "";
function generate(){
    button_generating = "is-loading"
    fetch(`${$ApplicationData.api_host}/public/api/v1/services/user/effi/tokens/${$ApplicationData.user.public_key}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
        method: "PATCH",
    })
    .then(( r )=>{
        return r.json();
    }).then(( data )=>{
        token_data = data
        button_generating = ""
    })
}

let FormDataApplication = new CustomStore({
    form: {
        install: false,
    }
}, ()=>{ FormDataApplication = FormDataApplication; })


let switchStatus = false;
function on_save(form, button) {
    ButtonStatus(button, "disabled");
    new ApplicationInstaller({
        ApplicationData: $ApplicationData,
        application_name: application_name,
        install: form.install,
        data: form,
        updater: ApplicationData,
        callback: function(data){
            ApplicationData.update("token_website", data.token);
            ApplicationData.update("app_store", data.builder_apps.data);
            ApplicationData.update("website_data", data.website);

            ButtonStatus(button, "enabled");
            button.setAttribute("disabled", "");
        }
    })
}


</script>

{#if token_data !== null}
    <section>
        <HeaderBoxProjects />
        <CommonAppsLayout FormDataApplication={FormDataApplication} application_name={application_name} switchStatus={switchStatus} application="{application_name}" on_save={on_save} type="default" steps={steps} logoUrl={logoUrl} underLineText={underLineText}>
            <section class="effi__container">
                <article class="effi__main">
                    <div class="effi__configuration">
                        <h2 class="is-size-5 has-text-gray has-text-weight-semibold mb-5 mx-5">Configuración</h2>
                        <div class="control mt-3">
                            <div class="field">
                                <p class="has-text-gray has-text-weight-medium mx-5">Dominio</p>
                                <div style="display: flex; align-items: center; position: relative;">
                                    <input type="text" class="input effi__input mx-5 has-text-gray has-text-weight-medium" value="https://{$ApplicationData.website_selected.subdomain}.mi3pod.com" readonly placeholder="Token de acceso">                            
                                    <i class="icon-tripod-duplicate copy-icon" on:click={(e) => copier(e)}></i>
                                </div> 
                            </div>
                        </div> 
                        <div class="control mt-3">
                            <div class="field">
                                <p class="has-text-gray has-text-weight-medium mx-5">Website Token</p> 
                                <div style="display: flex; align-items: center; position: relative;">
                                    <input type="text" class="input effi__input mx-5 has-text-gray has-text-weight-medium" value="{$ApplicationData.token_website}" readonly>                            
                                    <i class="icon-tripod-duplicate copy-icon" on:click={(e) => copier(e)}></i>
                                </div>  
                            </div>
                        </div>   
                        <div class="control mt-3">
                            <div class="field">
                                <p class="has-text-gray has-text-weight-medium mx-5">Token de acceso</p> 
                                <div style="display: flex; align-items: center; position: relative;">
                                    <input type="text" class="input effi__input mx-5 has-text-gray has-text-weight-medium" value="{token_data.token ?? "No se ha podido generar un token"}" readonly placeholder="Token de acceso">                            
                                    <i class="icon-tripod-duplicate copy-icon" on:click={(e) => copier(e)}></i>
                                    <button class="button btn-token has-text-weight-medium {button_generating}" type="button" on:click="{ ()=>{generate()} }">Generar nuevo</button>
                                </div>  
                            </div>
                        </div>                      
                    </div>
                </article>
            </section>
        </CommonAppsLayout>
    </section>
    {:else}
        <LoaderElement style_container="min-height:400px;"  />
{/if}
<style>

    .main{
        margin-top: 2.75rem;
    }
        
    .has-text-gray{
        color: var(--color-lightGrey);
    }

    .effi__container{
        display: flex;
        flex-direction: column;
    }

    .effi__input{
        font-size: 14px;
        padding: 20px 35px 20px 22px;
        width: 55%;
        border-radius: 8px;
    }

    .input:focus{
        outline: none !important;
    }

    .btn-token{
        background-color: var(--color-primary);
        color: #FFFFFF;
        font-size: 14px;
        width: 24%;
        height: 44px;
        padding: 3px 0;
        transform: translateX(-45px);
        border-radius: 0px 8px 8px 0px;
    }

    .copy-icon{
        transform: translateX(-53px);
        font-size: 18px;
        color: var(--color-lightGrey);
        cursor: pointer;
    }


    @media only screen and (max-width: 900px){
        .effi__input{
            width: 100%;
        }

        .effi__configuration h2{
            margin: 0 0 1.5rem 1.0625rem !important;
        }
        
        .effi__configuration p {
            margin: 0 0 0 1.0625rem !important;
        }
    }

</style>