<script>
 export let listHeader = [];
 export let paginatorData = {};
 export let updateAllChecks = [];

 let tableContainer;
 let checkListAll = [];


function setAllActives(e){
    let checks = [...tableContainer.querySelectorAll('.input-checkbox:not(.checkAll)')];

    if(e.target.dataset.active === "false") e.target.dataset.active = true;
    else e.target.dataset.active = false;

    if(e.target.dataset.active === "true"){
         checks.map((check) => {
            if(check.hasAttribute('disabled')){
                check.checked = false;
            }
            else{
                check.checked = true;
            }
         });     
    }
    else{
        checks.map((check) => {
            check.checked = false;
         });    
    }
    updateStateCheckboxes(e);
}

function updateStateCheckboxes(e){
    if(paginatorData?.data?.listData?.data.length > 0){
        paginatorData?.data?.listData?.data.filter((item) => {
            if(e.target.dataset.active === "true"){
                if(item.status === "pending"){
                    checkListAll = [...checkListAll, item.id.toString()];                   
                    return item.isChecked = true;
                }
            }
            else{
                checkListAll = [];
                return item.isChecked = false;
            }
        });
        updateAllChecks(checkListAll);
        paginatorData = paginatorData;
    }
}

 

</script>
<style>
.w-100{
    width: 100%;
}
.table thead th{
    font-size: 1rem;
    text-align: left;
    color: #415B76;
}

thead, tbody{
    text-align: center;
}

:global(.table td){
    vertical-align: middle !important;
}

</style>
  
<table class="table is-borderless w-100 is-hoverable" bind:this={tableContainer}>
    <thead class="w-100">
        <tr>
            {#each listHeader as data }
                <th class="fw800">
                    {#if data === "Checkbox"}
                         <input type="checkbox" class="input-checkbox checkAll" data-active="false" on:click={(e) => setAllActives(e)}>
                    {:else}
                         {data}                    
                    {/if}
                </th>
            {/each}
        </tr>
    </thead>
    <tbody class="has-border">
        <slot>

        </slot>
    </tbody>
</table>