import { writable } from 'svelte/store';

function appData() {


	const { subscribe, update } = writable({
        is_online: false,
        api_host: "https://dev.3pod.io/",
        website_data: {},
        app_store: [],
        go_to_project: function(){},
        token_website: null,
        project_selected: null,
        website_selected: null,
        has_projects: false,
        website_title: "",
        projects: [],
        path: "/",
        user: {},
        host: null,
        methods: {},
	});
	
	const dataApp = {
        subscribe: subscribe,
        addMethod: function(field, func){
            if(typeof func !== "function"){
                throw("Error, '"+field+"' is not a function");
            }
            update(( data )=>{
                data.methods[field] = func;
                return data;
            })
        },
        update: function(field, new_data){
            update(( data )=>{
                data[field] = new_data;
                return data;
            })
        },
        get: function(){
            let returns = {};
            update(function( data ){
                returns = data;
                return data;
            })
            return returns;
        }
    }

    return dataApp;
}


export const ApplicationData = appData();