<script>
import LocalFilter from './Dropdowns/LocalFilter.svelte';
import Styles from '../Styles.svelte';
import {turpial} from "../libraries/turpial.js";
import {clickOutside} from "../libraries/clickOutside.js";
export let size = "normal";
export let triggerStyle = "";
export let innerType = false;
export let name = "dropdown-"+turpial.random_string(6);
export let input_classes = "formElements";
export let selected = null;
export let on_mount = function(){};
export let label = "";
export let label_id = "label-"+turpial.random_string(6);
export let on_change = function(){};
export let classes = "default";
export let classes_container = "";
export let isSearch = false;
export let CustoStoreData;
export let list = null;


let new_array = [];
let parent_container = null;
let dropdown_element;
let isChanged = false;
list.map(( item )=>{
    try {
        if(typeof item.value !== "undefined" && typeof item.name !== "undefined"){
            new_array.push(item)
            return;
        }
    } catch (error) {
        
    }

    new_array.push({
        name: item,
        value: item,
    }) 
})

list = new_array;


let selected_item = null;
let selected_value = null;
let data_result = selected;
function ProcessList(){
    list.map(( item )=>{
        selected_item = list[0].name;
        selected_value = list[0].value;
        if(item.value === selected){
            selected_item = item.name;
            selected_value = item.value;
        }
    })
    on_mount(selected_item, selected_value);
    data_result = selected;
}

ProcessList();

$: {
    if(list != null){
        if (isChanged) {
            ProcessList();
        }
    }
}
let trigger;
let containerList;

/* Cambia el texto del dropdown hijo segun el click dado por el usuario
    Esta funcion iria por fuera donde se llame el DropDown
*/
/* document.addEventListener('click', e => {
    if(e.target.matches('.text-content-child')){
        let content = e.target.textContent;
        let element = e.target.parentElement.parentElement.parentElement.previousSibling.parentElement.children[0].children[0];

        element.textContent = content;
    }
}); */

/**
 * A function that is called when the user clicks on the dropdown. 
 * It adds the class is-drop-active to the dropdown.
*/
function handdleShowMore(element) {
    if (!element.classList.contains('is-drop-active')) {
        closeTabs(element.parentNode);
        element.classList.add("is-drop-active");
    }else{
        element.classList.remove("is-drop-active");
    }
	return;
}

/**
 * A function that closes all the dropdowns that are open.
*/
function closeTabs(container){
    if (innerType) {
        let containers = [...container.parentNode.parentNode.querySelectorAll(".is-dropdown-inner")];
        containers.map(( node )=>{
            node.classList.remove("is-drop-active");
        })
    }else{
        let containers = [...container.querySelectorAll(".is-dropdown")];
        containers.map(( node )=>{
            node.classList.remove("is-drop-active");
        })
    }
}


/**
 * A function that is called when the user clicks on an item in the dropdown.
 * It changes the value of the dropdown to the value of the item clicked.
*/
function change(name, value, event){
    isChanged = true;
    selected = name;
    data_result = value;
    selected_item = name;
    dropdown_element.classList.remove("is-drop-active");

    on_change(name, value, event);
}

$: {
    if(parent_container !== null){
        clickOutside([parent_container], (destroy)=>{
            try {
                dropdown_element.classList.remove("is-drop-active");
            } catch (error) {
                destroy();
            }
            
        })
    }
}


</script>
<style>
.container-dropdown{
    display: flex;
    align-items: center;
    grid-column-gap: 18px;
}
.is-dropdown.white .trigger{
    background-color: #fff;
    box-shadow: 5px 5px 32px hsl(0deg 0% 56% / 10%);
}
.is-dropdown .trigger{
    min-width: 165px;
    width: max-content;
    max-width: max-content;
}
.label-container{
    margin-right: 1rem;
}
.item-dropdown{
    background-color: #fff;
    transition: all 0.15s ease;
    min-width: 165px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.item-dropdown:hover{
    background-color: var(--color-grey);
    color: #fff;
    transition: all 0.15s ease;
    overflow: visible;
    white-space: break-spaces;
    text-overflow: unset;
}
.item-dropdown:first-child{
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}
.item-dropdown:last-child{
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}
.container-items{
    max-height: 200px;
    overflow-y: overlay;
}
.seach-container{
    padding: 0.35rem 0;
    background: white;
    z-index: 1000;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
}
</style>
<div class="container-dropdown {classes_container}" bind:this="{parent_container}">
    <div class="label-dropdown">
        <label class="has-text-gray fw600" for="{label_id}">
            {label}
        </label>
    </div>
    <div class="is-dropdown {classes}" bind:this="{dropdown_element}" style="width:{size}">
        <input type="hidden" id={label_id} bind:value="{data_result}" name="{name}" class="{input_classes}">
        <button  type="button" bind:this="{trigger}" class="trigger is-size-7" style={triggerStyle} on:click|stopPropagation|preventDefault={(e)=>{handdleShowMore(e.currentTarget.parentNode)}}>
            {#if (selected_item === null)}
                <slot></slot>
                {:else}
                    {selected_item}
            {/if}
            <i class="icon-tripod-arrow-small-down is-dropdown-icon" style="font-size: 10px;"></i>
            <!-- <div class="is-dropdown-icon"></div> -->
        </button>
        <ul bind:this="{containerList}" class="container-items custom-scrollbar-gray">
            {#if isSearch}
                <div class="seach-container flex-center">
                    <LocalFilter items={CustoStoreData}></LocalFilter>
                </div>
            {/if}
            {#each list as item}
                <li class="item-dropdown pointer is-size-7" data-value="{item.value}" on:click|preventDefault="{function(event){{ change(item.name, item.value, event) }}}">
                    {item.name}
                </li>
            {/each}
        </ul>
    </div>
</div>
