import CommonLayout from "../layouts/CommonLayout.svelte";

/**
 * 
 * @param {ClassInstance} AppRoute Contains methods for redirections and setters for sub routes
 * @var {Object} AppRoute.path contains current parameters and url path. 
 * @var {Object} AppRoute.parameters function to create a return result with all url paramters.
 * @var {function} page.route use this method to create a redirection. example: page.route("user/dashboard")
 * @returns {Object}
 */
const IndexRoute = ()=>{
    return {
        type: "public",
        template: CommonLayout,        
        props: {
            title: "Proyectos",
            description: "Aquí puedes crear y gestionar todo lo relacionado con tu página web",
            routes: [
                {
                    name: "Home",
                    path: __HOST,
                },
                {
                    name: "Proyectos",
                    path: "/"
                },
            ],
        },
        _render: function() {
            return import("../views/Index.svelte").then(( results )=>{
                return results;
            })
        },
    }
};



export default IndexRoute;