<script>
import { RouterStore } from './../../RouterStore.js';
import { page } from './../../turpial.route.store.js';
import { InlineRoute } from './../../inline.routes.js';
import { ApplicationData } from './../../data.application.route.js';
import ComponentsGroup from "../ComponentsGroup.svelte";
import Dropdowns from "../dropdowns.svelte";
let projects = $ApplicationData.projects ?? null;

let show = false;


//5OITonYkGPaRtFkkemjhbpV3bKHRLj_H
let project_list = [];
let sites = [];
fetch(`${$ApplicationData.api_host}public/api/v1/services/user/projects/${$ApplicationData.user.public_key}`)
.then((r)=>{
    
    return r.json();
})
.then((r)=>{
    ApplicationData.update("projects", r.projects ?? []);
    ApplicationData.update("has_projects", true);
    show = true;
    
    project_list = $ApplicationData.projects.map(( item )=>{
        return {
            name: item.name,
            value: item.id,
        }
    })
    
    sites = $ApplicationData.projects[0].websites.map(( item )=>{
        return {
            name: item.name,
            value: item.id,
        }
    })
})



let websites = null;
function changeProject(name, value){
    $ApplicationData.projects.map(( project )=>{
        
        if(project.id === value){
            websites = project.websites;

            ApplicationData.update("project_selected", {
                name: project.name,
                value: value,
            })
        }
    })

    
    websites = websites.map(( website )=>{
        return {
            name: website.name,
            subdomain: website.subdomain,
            value: website.id,
        }
    })
}

let first_time = true;



function changeWebsite(name, value){
    /**
     * stay on same view
    */
   
    if(value === ($ApplicationData?.website_selected?.value ?? null)){ return; }
    
    console.log(websites, Object.values($RouterStore.parameters).length)
    if(typeof Object.values($RouterStore.parameters).length > 0){
        page.route("projects");
    }
    

    let selected = websites.find(( website )=>{
        if(website.value === value){
            return website;
        }
    })
    
    ApplicationData.update("website_title", name);
    ApplicationData.update("website_selected", {
        name: selected.name,
        value: selected.value,
        subdomain: selected.subdomain,
    })

    $ApplicationData.methods.website_changer();
    
}

</script>
{#if show}
    <div class="columns is-multiline" style="width: 100%;">
        <div class="column is-6"> 
        <ComponentsGroup gap="3.25rem">
            <Dropdowns label="Proyectos" classes_container="" on_change={changeProject} on_mount={changeProject} classes="white" innerType={true} list={project_list}>
                Projectos
            </Dropdowns>
            {#if websites !== null}
                <Dropdowns label="Sitios" classes_container="" on_change={changeWebsite} on_mount={changeWebsite} classes="white" innerType={true} list={sites}>
                    Sitios
                </Dropdowns>
            {/if}
        </ComponentsGroup> 
        </div>
        <div class="column is-6">
            <div class="buttons is-right">
                <button on:click="{function(e){ $ApplicationData.go_to_project() }}" type="button" class="button brand is-primary is-rounded">
                    Ir al constructor
                </button>
            </div>
        </div>
    </div>
{/if}