<script>
export let Class = "";
export let id = "";
export let style = "";
export let type = "primary-color";
export let path = "";
export let to = null;
export let on_click = function(){};
let classes = "button is-primary";
import { page } from '../../turpial.route.store.js';
if(typeof to === "string" || typeof to === "number"){
	let current = $page.path;
	current = current.slice(-1) === "/" ? current+"" : current+"/";
	path = current + `${to}`
}
if(type === "linkContainer"){
	classes = "linkContainer";
}
</script>
<style>
a {
	cursor: pointer;
	text-decoration: none;
	border: 0;
	border-radius: 10px;
	transition: all 0.1s ease;
}
a.none{
	background: transparent;
	color: #415B76;
}
a.linkContainer{
	width: 100%;
	height: 100%;	
}
a.linkContainer.fit-content{
	width: fit-content;
	height: auto;	
}
a.primary-color {
	background: linear-gradient(45deg, #277aff, #1ba3ff, #17b2ff);
	color:#fff;
	box-shadow: inset 0 13px 27px rgb(27 59 98 / 25%);
}
a.primary-color *{
	color:#fff;
}
a.primary-color:hover {
	transform: scale(1.02);
	transition: all 0.1s ease;
}

a.secundary-color {
	background-color: var(--color-secundary);
	color: var(--color-grey);
}
a.secundary-color *{
	color: var(--color-grey);
}
a.secundary-color:hover {
	color: var(--color-grey);
	transform: scale(1.02);
	transition: all 0.1s ease;
}

a.neutral-color {
	text-decoration: underline;
	background-color: transparent;
	color: var(--color-primary);
}
a.neutral-color *{
	color: var(--color-primary);
}
a.neutral-color:hover {
	text-decoration: none !important;
	transition: all 0.1s ease;
}

a.success-color {
	background-color: var(--color-success);
    color: #ffffff;
}
a.success-color *{
	color: #ffffff;
}
a.success-color:hover {
	color: #ffffff;
	transform: scale(1.02);
	transition: all 0.1s ease;
}
a.is-rounded{
	border-radius: 40px;
}
a.is-shadowless:focus, a.is-shadowless{
	box-shadow: none;
}
</style>
<a on:click={on_click} class="{Class} {type} {classes}" style="{style}" id="{id}" href="{page.getPath(path)}" on:click|preventDefault={()=>{ page.route(path); }}>
	<slot></slot>
</a>