/** Dispatch event on click outside of nodes */
export function clickOutside(nodes, onClicked){
  onClicked = onClicked||function(){};
  if(Array.isArray(nodes) === false){
    nodes = [nodes];
  }

  let destroy = function() {
    document.removeEventListener('click', catchClick, true);
  }

  const catchClick = function(event){
    let hasBeenCliked = false;
    nodes.map(( node )=>{
      if(node === null){ return; }
      try {
        if(node.contains(event.target)){
          hasBeenCliked = true
        }
      } catch (error) {
        destroy();
      }
    })
    if(hasBeenCliked === false){
      onClicked(destroy);
    }
  }
  document.addEventListener("click", catchClick, true);
}