
class FetchApp 
{
    
    constructor(customstore, callback)
    {
        this.store = customstore;
        this.conditions = customstore.data.conditions ?? [];
        this.parameters = "";
        this.headers = customstore.data.headers ?? {};
        this.callback = callback || function(){};
        this.url = customstore.data.url;
        this.items = 0;
    }
    fetch(callback)
    {
        const app = this;
        callback = callback || function(){}
        if(typeof this.before_fetch === "function"){
            this.before_fetch();
        }
        
        this.conditions = this.store.data.conditions ?? [];
        this.conditions = Object.values(this.conditions).map(( condition, key )=>{
            let name = Object.keys(this.conditions)[key];
            this.parameters += `&${name}=${condition}`
        })

        this.parameters = `?${this.parameters}`
        this.parameters = this.parameters.split("?&").join("?");
        this.parameters = this.parameters.split("??").join("?");
        fetch(this.url+this.parameters, {
            headers: this.store.data.headers
        })
        .then(response => response.json())
        .then(results => {
/*             console.log(results); */
            this.items = results.items;
            results.data.forEach(element => {
                let statusHanddler = {
                    icon: "",
                    color: "",
                    text: ""
                }
                switch (element.status) {
                    case "pending":
                            statusHanddler.icon = "icon-tripod-package"
                            statusHanddler.color = "var(--color-primary)"
                            statusHanddler.text = "En preparación"
                            element.statusHanddler = statusHanddler;
                        break;
                    case "completed":
                        //3ok
                            statusHanddler.icon = "icon-tripod-shipped"
                            statusHanddler.color = "var(--color-success)"
                            statusHanddler.text = "Entregado"
                            element.statusHanddler = statusHanddler;
                        break;
                    case "shipped":
                        //1
                            statusHanddler.icon = "icon-tripod-truck-moving"
                            statusHanddler.color = "var(--color-primary)"
                            statusHanddler.text = "En camino"
                            element.statusHanddler = statusHanddler;
                        break;
                    case "canceled":
                            statusHanddler.icon = "icon-tripod-warning"
                            statusHanddler.color = "var(--color-danger)"
                            statusHanddler.text = "Cancelado"
                            element.statusHanddler = statusHanddler;
                        break;
                    default:
                        break;
                }
            });  
            
            app.callback(results);
            callback(results)
        })
    }
}
export {FetchApp};