<script>
	import DetailLayout from './DetailLayout.svelte';
	import HeaderTabs from './../views/projects/HeaderTabs.svelte';
import { RouterStore } from './../RouterStore.js';
import LoaderElement from './../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../data.application.route.js';
import ProjectsDropdown from './../components/Dropdowns/ProjectsDropdown.svelte';
import { InlineRoute } from './../inline.routes.js';
import { turpial } from '../libraries/turpial';


let description = $RouterStore.description ?? "";
let title = $RouterStore.title ?? "";
let routes = $RouterStore.routes ?? [];
let parameters = $RouterStore.parameters ?? {};

let show = false;
let fetched = false;

console.log($RouterStore);

if(($InlineRoute?.path ?? "") === ""){
    show = true;
}

ApplicationData.addMethod("website_changer", function(){
    let id = btoa(`${$ApplicationData.project_selected.value}_${$ApplicationData.website_selected.value}`);
    show = false;
    
    turpial.fetch({
        url: `${$ApplicationData.api_host}public/api/v1/services/user/website_selected/${id}`,
        type: "text",
        method: "GET",
        ready: function(responses){                
            let response = JSON.parse(responses[0]);
            ApplicationData.update("token_website", response.token);
            ApplicationData.update("app_store", response.builder_apps.data);
            ApplicationData.update("website_data", response.website);
            show = true;
            fetched = true;
        }
    })
})


if($ApplicationData)


if(typeof $RouterStore.parameters?.order === "string"){
    title = `Orden: #00${$RouterStore.parameters.order}`;
    description = "";
}
</script>
<style>
.general-container{
    height: 100%;
    background: #F5F6FA;
    color: #415B76;
    padding: 6.50rem 1.25rem;
}
.routes-container div p{
    color: #35465B;
}
.general-card-container{
    background: white;
    border-radius: 20px;
    min-height: 300px;
    height: fit-content;
    padding: 36px 4.25rem;
}
.data-container{
    padding: 20px 0;
}
@media only screen and (max-width: 1023px){
    .general-container{
        width: 100%;
        margin-left: 0;
    }
}
@media only screen and (max-width: 450px){
    .general-card-container{
        width: 98% !important;
    }
}
.general-container:global(.home-styles .general-card-container){
    background-color: #fff !important;
    background: url('https://i.ibb.co/tKHQLHn/900.jpg'), #fff !important;
    background-repeat: no-repeat !important;
    min-height: 580px !important;
    background-size: contain !important;
}
@media only screen and (max-width:1023px){
    .general-container:global(.home-styles .general-card-container){
        background-color: #fff !important;
        background: #fff !important;
    }
}
</style>
{#if $InlineRoute.path === "projects/forms_report" || $InlineRoute.path === "projects" || $InlineRoute.path === "projects/sales_report"}
    <section class="general-container flex-column-space-evenly {($InlineRoute.path === "/" || $InlineRoute.path === "") ? 'home-styles' : ''}">
        <main class="columns is-multiline is-centered">
            <div class="column is-9">
                <div class="data-container">
                    <div class="routes-container">
                        {#if routes.length >= 1 }
                                <div class="flex-center" style="width: fit-content;">
                                    <p class="has-text-weight-medium">
                                        {#each routes as route, key}
                                            <a href="{route.path}">
                                                {route.name}                                             
                                            </a>
                                            {#if (key+1) !== routes.length}
                                                <span>> </span>
                                            {/if}
                                        {/each}
                                    </p>
                                </div>
                        {/if}
                        <div style="padding: 10px 0;">
                            {#if title != ""}
                                <h1 class="has-text-weight-medium is-size-3-desktop is-size-4-tablet is-size-5-mobile" style="font-size: 36px;">{title}</h1>
                            {/if}
                            {#if description != ""}
                                <p>{description}</p>
                            {/if}
                        </div>
                        {#if ($InlineRoute?.path ?? "") !== ""}
                            <ProjectsDropdown /> 
                        {/if}
                    </div>
                </div>
                <div class="general-card-container has-shadow">
                    {#if show}
                            {#if ($InlineRoute?.path ?? "") !== ""}
                                <HeaderTabs />
                            {/if}
                            
                            <slot>
                                <!-- EN ESTE SLOT SE INSERTARIA EL ELEMENTO DE LA VISTA Routes que corresponda -->
                            </slot>
                        {:else}
                            <LoaderElement style_container="min-height:400px;"/>
                    {/if}
                    
                </div>
            </div>
        </main>
    </section>
    {:else}
        <DetailLayout>
            <slot></slot>
        </DetailLayout>
{/if}