<script>
import { FetchApp } from "../../libraries/fetchApp";
import AppForms from "../../libraries/coreApp/AppForms"
export let paginatorData;
export let list_application_fetch;

let form;
let current = 1;


/**
 *  A function that is called when the user clicks on the pagination buttons. 
 *  It is responsible for updating the paginatorData object with the new currentPage and offset values.
 * @param position value that define position of paginator and it is passed by parameter
 */
function handdlePagination(position){
    if (position === "input") {
        let data = AppForms.retrieve({
            form: form,
            targets: ".formsElements"
        })

       data.currentPage = Number(data.currentPage);

        try {
            if (data.currentPage > 0 && data.currentPage <= paginatorData.data.limit) {
                paginatorData.setJson("currentPage", parseInt(data.currentPage));
                let offset = parseInt(paginatorData.data.currentPage) * paginatorData.data.conditions.perpage - paginatorData.data.conditions.perpage;
                paginatorData.update((object)=>{
                    object.conditions.offset = offset;
                    return object; 
                })
                                
            }else if (data.currentPage > paginatorData.data.limit) {
                data.currentPage = paginatorData.data.limit;
                paginatorData.setJson("currentPage", paginatorData.data.limit);
                let offset = paginatorData.data.limit * paginatorData.data.conditions.perpage - paginatorData.data.conditions.perpage;
                paginatorData.update((object)=>{
                    object.conditions.offset = offset;
                    return object; 
                })

            }else if (data.currentPage < 1) {
                data.currentPage = 1;
                paginatorData.setJson("currentPage", 1);
                let offset = 1 * paginatorData.data.conditions.perpage - paginatorData.data.conditions.perpage;
                paginatorData.update((object)=>{
                    object.conditions.offset = offset;
                    return object; 
                })

            }
        } catch (error) {
            console.log(error);
        }
    } else {
        if (position === "left" && paginatorData.data.currentPage > 1) {
            paginatorData.setJson("currentPage", paginatorData.data.currentPage -= 1);
            let offset =  paginatorData.data.conditions.offset - paginatorData.data.conditions.perpage;
            paginatorData.update((object)=>{
                object.conditions.offset = offset;
                return object; 
            })
        }else if (position === "right" && paginatorData.data.currentPage < paginatorData.data.limit) {
            paginatorData.setJson("currentPage", paginatorData.data.currentPage += 1);
            let offset =  paginatorData.data.conditions.offset + paginatorData.data.conditions.perpage;
            paginatorData.update((object)=>{                
                object.conditions.offset = offset;
                return object; 
            })
        } else {
            if (position != "left" && position != "right") {
                paginatorData.setJson("currentPage", position);
                let offset =  position * paginatorData.data.conditions.perpage - paginatorData.data.conditions.perpage;
                paginatorData.update((object)=>{
                    object.conditions.offset = offset;
                    return object; 
                })
            }
        }
    }
    console.log(list_application_fetch);
    list_application_fetch.fetch();
}

</script>

<style>
    .btn-arrow{
        background: transparent;
        border: none;
        color: #B4C0C9;
    }
    .input-paginator{
        border: 1px solid #B4C0C9;
        border-radius: 8px;
        height: 2.125rem;
        width: 2.125rem;
        outline: none;
        text-align: center;
    }
    .icon-size{
        font-size: 14px;
    }
</style>

<div class="flex-center-flex-end">
    <span class="is-size-7">{paginatorData.data.currentPage} of {paginatorData.data.limit}</span>
    <button type="button" class="btn-arrow" on:click={() => handdlePagination("left")} disabled={ paginatorData.data.currentPage === 1 }><i class="icon-tripod-navigation-left icon-size"></i></button>
        <form bind:this={form} action="POST" on:submit|preventDefault={() => handdlePagination("input")}>
            <input type="number" class="formsElements input-paginator" name="currentPage" id="" value={paginatorData.data.currentPage}>
        </form>
    <button type="button" class="btn-arrow" on:click={() => handdlePagination("right")}><i class="icon-tripod-navigation-right icon-size"></i></button>
</div>
