import { writable } from 'svelte/store';

function routerStore() {


	const { subscribe, update } = writable({
        parameters: {},
    });
	
	const routes = {
        subscribe: subscribe,
        update: (new_data)=>{
            update(( data )=>{
                data = new_data;
                return data;
            })

            return new_data;
        }
    }

    return routes;
}


export const RouterStore = routerStore();