<script>
import CountriesDropdown from './../../../components/Dropdowns/CountriesDropdown.svelte';
import SimpleModal from './../../../components/Modals/SimpleModal.svelte';
import { RouterStore } from './../../../RouterStore.js';
import { ApplicationData } from './../../../data.application.route.js';
import { InlineRoute } from '../../../inline.routes.js';
import { page } from '../../../turpial.route.store.js';
import AppForms from '../../../libraries/coreApp/AppForms.js';
import LoaderElement from '../../../components/Elements/LoaderElement.svelte';

let form = null;
let processForm;

fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/forms/${$RouterStore.parameters.form}`, {
    headers: {'X-Token-Website': $ApplicationData.token_website}
})
.then((r)=>{
    return r.json();
}).then((r)=>{
    form = r;
    console.log(form);
})

const search = () => {
    let data = AppForms.retrieve({
        form: processForm,
        targets: ".formsElements"
    })
    console.log(data);
}

function on_close() {
    page.route("projects/forms_report");
}

function contriesGetter(name, value, event) {
    form.countrie = {
        name: name,
        value: value
    }
    console.log(form);
}

</script>
<style>
/* .details-container{
    
} */
.details-container{
    color: #415B76;
}
.details-container .is-line a{
    text-decoration: none;
    color: #415B76;
}
.lines-container{
    margin-top: 2rem;
    padding: 0 2rem;
    max-height: 400px;
    overflow-y: scroll;
}
.is-line{
    width: 100%;
}
.lines-container .is-line p{
    margin-bottom: 0.35rem;
}
.lines-container .is-line.line{
    padding: 1rem;
}
.lines-container .is-line.line .form-text{
    font-weight: 500;
}
.modal-content{
    width: 100%;
    height: 100%;
    min-height: 350px;
    color: #415B76;
}
.modal-content div div{
    padding: 1rem 0;
}
.modal-content div div p{
    font-size: 28px;
}
.modal-content div div div{
    padding: 1rem;
    color: #296FFF;
}
</style>
{#if form !== null}
    <form bind:this={processForm} on:submit|preventDefault={()=>{search()}} class="details-container full-size flex-column">
        <div class="is-line flex-center-flex-start">
            <a href="#path:/projects/forms_report/{form?.id}" class="flex-center-flex-start">
                <i class="icon-tripod-arrow-back is-size-2"></i>
                <p style="margin-left: 1rem;">volver</p>
            </a>
        </div>
        <div class="lines-container custom-scrollbar-gray full-size flex-column-flex-start">
            <div class="is-line flex-column mb-2">
                <h1 class="fw500 is-size-2">Procesar Orden</h1>
                <p>Revisa los datos antes de enviar el pedido a la plataforma de Efficommerce</p>
            </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Nombres Completos</p>
                    <input id="names" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Teléfono</p>
                    <input id="tel" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Celular</p>
                    <input id="cel" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Email</p>
                    <input id="email" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-center-space-between">
                    <p class="form-text">País</p>
                    <CountriesDropdown on_change={contriesGetter}/>
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Provincia</p>
                    <input id="provincia" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Ciudad</p>
                    <input id="city" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Dirección completa</p>
                    <input id="dir" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-center">
                    <p class="fw600" style="font-size: 20px;">Productos</p>
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">ID del producto</p>
                    <input id="product-id" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Precio unitario</p>
                    <input id="price" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Cantidad</p>
                    <input id="amount" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
                <div class="is-line line flex-column-flex-start">
                    <p class="form-text">Valor total</p>
                    <input id="totals" class="input is-line formsElements" style="resize: none;" name="names" value="value" />
                </div>
        </div>
        <div class=".is-line flex-center">
            <SimpleModal onOpen={console.log("open")} let:hide={hide} onClose={on_close} triggerClass="w-100" styles="max-width: 600px;" classesHeader="" classesContent="px-5 pt-1 w-100 alternative" id="modal_1" closeOnClickOverlay="false">
                <div slot="content">
                    <div class="modal-content flex-center">
                        <div class="flex-column">
                            <img src="https://i.ibb.co/HgYpsR4/check.png" alt="check" border="0" width="85">
                            <div class="flex-column">
                                <p class="fw600">Las ordenes han sido</p>
                                <p class="fw600">procesadas correctamente</p>
                                <div class="flex-center" style="margin-bottom:2rem;">
                                    <i class="fas fa-question"></i>
                                    <p style="font-size: 16px; margin-left:10px;">En Preparación</p>
                                </div>
                            </div>
                            <button class="button brand is-rounded is-primary" on:click={()=>{on_close(); hide()}}>
                                Aceptar
                            </button>
                        </div>
                    </div>
                </div>
                <div slot="trigger">
                    <button class="button brand is-medium is-rounded is-primary" on:click={()=>{search()}}>
                        Enviar Pedido
                    </button>
                </div>
            </SimpleModal>
        </div>
    </form>
    {:else}
        <LoaderElement  style_container="min-height:400px;"/>
{/if}