<script>
import {parsePaginatorData} from "./parsePaginatorData.js"
export let paginator = {};
export let getList = ()=>{};
let current = 1;
paginator.selfPaginator = {};
/**
    * Se le pasa al slot.default la variable paginator
    * SI solo SI hay un cambio en paginator el contenido del controlador de la paginación cambiará.
*/

function handdlePagination(currentPos){
    if (paginator.selfPaginator.type === "supabase") {
        if (currentPos === "left" && current > 1) {
            current -=1;
            paginator.pos = paginator.perpage * paginator.selfPaginator.position - paginator.perpage;
            paginator.selfPaginator.position = current;
        }else if (currentPos === "right" && paginator.selfPaginator.position < paginator.selfPaginator.dataList.limit) {
            current += 1;
            paginator.selfPaginator.position = current;
            paginator.pos = paginator.perpage * paginator.selfPaginator.position - paginator.perpage;
        } else {
            if (currentPos != "left" && currentPos != "right") {
                current = currentPos;
                paginator.selfPaginator.position = currentPos;
                paginator.pos = paginator.perpage * current - paginator.perpage;
            }
        }
        paginator = paginator;
        current = current;
    }else if (paginator.selfPaginator.type === "default") {
        if (currentPos === "left" && paginator.position > 1) {
            paginator.position -= 1;
            paginator.selfPaginator.position -= 1;
            current -=1;
        }else if (currentPos === "right" && paginator.position < paginator.dataList.limit) {
            paginator.position += 1;
            paginator.selfPaginator.position += 1;
            current += 1;
        } else {
            if (currentPos != "left" && currentPos != "right") {
                paginator.position = currentPos;
                paginator.selfPaginator.position = currentPos;
                current = currentPos;
            }
        }
        paginator = paginator;
        current = current;
    }
    getList();
}


function onData(paginator){
    getList(()=>{
        paginator = parsePaginatorData(paginator, current);
    });
}
onData(paginator);
</script>
<style>
    button{
        border: none;
        padding: 1.2rem;
        /* padding-left: 1.2rem;
        padding-right: 1.2rem; */
        color: var(--color-blue-second);
        box-shadow: 5px 5px 20px rgba(142, 142, 142, 0.17);
        margin: 3px;
    }
    button:hover{
        background-color: var(--color-extralightGrey);
    }
</style>
<slot paginator={paginator}></slot>
{#if paginator.selfPaginator?.dataList?.data?.length}
    <div class="full-size">
        <div style="width:100%; height:4rem;" class="flex-center">
            <button on:click={() => handdlePagination("left")} class="button is-small" type="button">
                <i class="fas fa-angle-left is-size-5"></i>
            </button>
            {#if paginator.selfPaginator.dataList.limit !== 1}
                {#if paginator.selfPaginator.position >= 1}
                    <button on:click={() => handdlePagination(1)} class="button is-small {paginator.selfPaginator.position === 1 ? "is-primary" : ""}" type="button">
                        <p class="is-size-6">{1}{current > 1 ? "..." : ""}</p>
                    </button>
                {/if}
                {#if paginator.selfPaginator.position === current && current > 1 && current < paginator.selfPaginator.dataList.limit}
                    <button class="button is-small {current=== paginator.selfPaginator.position ? "is-primary" : ""}" type="button"><p class="is-size-6">{current}</p></button>
                {/if}
                {#each paginator.selfPaginator.dataList.navigation as row}
                    {#if row !== 1 && row !== paginator.selfPaginator.dataList.limit && current !== row}
                        <button on:click={() => handdlePagination(row)} class="button is-small {row === paginator.selfPaginator.position ? "is-primary" : ""}" type="button"><p class="is-size-6">{row}</p></button>
                    {/if}
                {/each}
                {#if paginator.selfPaginator.position <= paginator.selfPaginator.dataList.limit}
                    <button on:click={() => handdlePagination(paginator.selfPaginator.dataList.limit)} class="button is-small {paginator.selfPaginator.position === paginator.selfPaginator.dataList.limit ? "is-primary" : ""}" type="button">
                        <p class="is-size-6">{current < paginator.selfPaginator.dataList.limit ? "..." : ""}{paginator.selfPaginator.dataList.limit}</p>
                    </button>
                {/if}
            {:else}
                <button class="button is-small {paginator.selfPaginator.position === paginator.selfPaginator.dataList.limit ? "is-primary" : ""}" type="button">
                    <p class="is-size-6">{current < paginator.selfPaginator.dataList.limit ? "..." : ""}{paginator.selfPaginator.dataList.limit}</p>
                </button>
            {/if}
            <button on:click={() => handdlePagination("right")} class="button is-small" type="button">
                <i class="fas fa-angle-right is-size-5"></i>
            </button>
        </div>
    </div>
{/if}
