const AppDate = {
    /**
     * options availables are: seconds (default), miliseconds, date
     */
    unixtime_default: "default",

    /**
     * 
     * @param {Unixtime} unixtime in seconds
     * @returns unixtime in miliseconds (to execute new Date js, properly).
     */
	convert: function(date_or_unixtime, type){
        if(type === "seconds" || type === "default"){
            return new Date(date_or_unixtime * 1000);
        }
		if(type === "miliseconds" || type === "date"){
            return new Date(date_or_unixtime);
        }
	},
	months: ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"],

    /**
     * does not have any functionability outsite this component.
     * @param {Date} date 
     * @returns 
     */
	day: function(date){
		date = `0${date}`;
		date = date.split("");
		if(date.length > 2){
			delete date[0];
		}
		return date.join("");
	},
    /**
     * format date with time
     * @param {Date} date 
     * @returns 
     */
	formatAMPM: function(date) {
		var hours = date.getHours();
		var minutes = date.getMinutes();
		var ampm = hours >= 12 ? 'pm' : 'am';
		hours = hours % 12;
		hours = hours ? hours : 12; // the hour '0' should be '12'
		minutes = minutes < 10 ? '0'+minutes : minutes;
		var strTime = hours + ':' + minutes + ' ' + ampm;
		return strTime;
	},
    /**
     * 
     * @param {epoch} unixtime unixtime (seconds)
     * @returns Date : Time
     */
	full: function( unixtime, type ){
        type = type || this.unixtime_default;
		var time = this.convert( unixtime, type );
		
		return `${this.months[time.getMonth()]}-${this.day(time.getDate())}-${time.getFullYear()} : ${this.formatAMPM(time)}`
	},
    /**
     * 
     * @param {epoch} unixtime unixtime (seconds)
     * @returns Time
     */
	onlytime: function(unixtime, type){
        type = type || this.unixtime_default;
		var time = this.convert( unixtime, type );
		return `${time.getHours()}:${time.getMinutes()}:${time.getSeconds()}`;
	},
    /**
     * 
     * @param {epoch} unixtime unixtime (seconds)
     * @param {string} type format like: d-m-Y or Y-m-d or m-d-Y default is d-m-Y
     * @param {string} format like: d-m-Y or Y-m-d or m-d-Y default is d-m-Y
     * @returns Date wtith format you set.
     */
	onlydate: function(unixtime, type, format){
        type = type || this.unixtime_default;

		var time = this.convert( unixtime, type );

		format = format || "d-m-Y";
		var date = {
			Y: time.getFullYear(),
			m: this.months[time.getMonth()],
			d: this.day(time.getDate()),
		};
		format = format.split("-");
		var getDate = [];
		format.map(function( data ){
			if(typeof date[data] === "undefined"){
				console.warn("undefined "+data+" date value.")
				return;
			}
			getDate.push(date[data])
		})
		return getDate.join("-");
	}
}

const ButtonStatus = function(button, status){
	if(button === null){
		return;
	}
	if(status === "loading" || status === "enabled"){
		button.classList.remove("is-loading");
		button.removeAttribute("disabled");
	}else{
		button.classList.add("is-loading");
		button.setAttribute("disabled", "");
	}
	
}

export {coreApp, AppDate, ButtonStatus};