<script>
import ListReport from './components/ListReport.svelte';
import Paginator from '../../../components/paginator/Paginator.svelte';
import SearchInput from '../../../components/Inputs/searchInput.svelte';
import { CustomStore } from "../../../libraries/customStore/CustomStore.js";
import { FetchApp } from "../../../libraries/fetchApp";
import ComponentsGroup from '../../../components/ComponentsGroup.svelte';
import Dropdowns from '../../../components/dropdowns.svelte';
import HeaderBoxProjects from '../HeaderBoxProjects.svelte';
import { ApplicationData } from '../../../data.application.route';
import NoElementsTable from '../../../components/Elements/NoElementsTable.svelte';
import LoaderElement from '../../../components/Elements/LoaderElement.svelte';
   
let value;
let title = 'Reporte de formularios completados';
let description = 'Aquí puedes crear y gestionar todo lo relacionado con tu página web';
let currentPage = 1;

let formsPaginator = new CustomStore( 
/*     https://app.3pod.io/public/api/v1/services/builder/forms */
    {
        currentPage: 1,
        limit: 0,
        url: `${$ApplicationData.api_host}public/api/v1/services/builder/forms`,
        headers: {
            'X-Token-Website': $ApplicationData.token_website,
        },
        conditions: {
            search: "",
            perpage: 10,
            offset: 0,
            sortby: "asc",
            orderby: "created_at",
            fields: "name,fields",
            from: "",
            until: "",
        },
        statusOrders: {
            pending: 0,
            shipped: 0,
            completed: 0,
            canceled: 0,
            processing: 0
        }
    },
    function(){
        formsPaginator = formsPaginator;
    }
);

let list = [];
const list_application_forms = new FetchApp(formsPaginator, (data)=>{
    formsPaginator.setJson("listData", data);
    list = data;
    formsPaginator.setJson("limit", Math.ceil(data.items / formsPaginator.data.conditions.perpage));
})

list_application_forms.before_fetch = ()=>{
    formsPaginator.update(function(data){
        data.headers = {
            'X-Token-Website': $ApplicationData.token_website,
        }
        return data;
    });
}
function orderBy(name, value, event) {
    formsPaginator.update((object)=>{
        object.conditions.sortby = value;
        return object; 
    })
    list_application_forms.fetch();
}

$: {
    if($ApplicationData.website_selected){
        list_application_forms.fetch();
    }
}
</script>
<style>
.title{
    color: #415B76;
    font-size: 1.375rem;
}
.gap-8{
    gap:24px
}
@media screen and (max-width:950px) {
    .flex-column-tablet{
        flex-direction: column;
        gap: 24px;
    }
}
@media screen and (max-width:600px) {
    .flex-column-mobile{
        flex-direction: column;
        gap: 24px;
    }
}
</style>
<HeaderBoxProjects paginatorData={formsPaginator} list_application_fetch={list_application_forms}/>
<section>
    <div class="mb-6 mt-3 px-2">
        <SearchInput paginatorData={formsPaginator} list_application_fetch={list_application_forms}/>
    </div>
    <div class="flex-center-space-between flex-column-tablet">
        <div class="flex-center-space-between gap-8 flex-column-mobile mb-3">
            <!-- FILTER HERE -->
            <!-- <div class="">
                <span class="is-flex"><i class="fas fa-question is-size-5 has-text-grey mr-4" ></i>Filtro</span>
            </div> -->
            <ComponentsGroup>
                <Dropdowns label="Ordenar por" on_change={orderBy} list={[
                    {
                        name: "Ascendente",
                        value: "asc"
                    },
                    {
                        name: "Descendente",
                        value: "desc"
                    },
                ]}>
                    Ascendente.
                </Dropdowns>
            </ComponentsGroup>
        </div>
        <Paginator paginatorData={formsPaginator} />
    </div>
    {#if list?.data?.length > 0}
        <div class="table-container" style="overflow-x: auto;">
            <ListReport paginatorData={formsPaginator}/>
        </div>
        {:else if  list.items === 0}
            <NoElementsTable />
        {:else}
            <LoaderElement style_container="min-height:400px;" />
    {/if}
    <!--  --> 
    <div class="flex-start-flex-end">
        <Paginator paginatorData={formsPaginator} list_application_fetch={list_application_forms}/>
    </div>  
</section>
