
<script>
	import { RouterStore } from './../RouterStore.js';
	import DefaultLayout from './../layouts/DefaultLayout.svelte';
import RouterConditions from './../router.conditions.js';
import { page } from '../turpial.route.store.js';
import Routes from '../routes.js';
import Loader from '../components/Loader.svelte';


export let on_controller = "";
export let on_action = "";
export let on_parameters = "";
export let CustomRoute = false;

const reserved_words = /_render|_limit_params|template|props|params/;

export let path = "";
let allFails = true;
let routes = path.split("/");
let controller_name = "";
let action_name = "";
let parameters = [];
routes.map(( selector, key )=>{
	if(key === 0){
		controller_name = selector;
		return;
	}
	if(key === 1){
		action_name = selector;
		return;
	}
	parameters.push(selector);
})
if(controller_name === ""){
	controller_name = "index";
}
let views = {
	controller_name: controller_name,
	action_name: action_name,
	parameters: parameters,
}

views = `${views.controller_name}/${views.action_name}/${views.parameters.join("/")}`;
views = views.split("///").join("/");
views = views.split("//").join("/");


if(path !== ""){
	on_controller = controller_name;
	on_action = action_name;
	on_parameters = parameters;
}

let layout = DefaultLayout;
let props = {};
let params = [];
function showView(structure, return_layout = false){
	
	if(structure.path.search(reserved_words) >= 0){
		console.error("Reserved Words Found.")
		return Routes.errors["404"]._render();
	}
	
	RouterConditions(structure.controller_name, structure.action_name, structure.path);

	


	let view = null;
	if(typeof Routes[structure.controller_name] === "object"){
		if(typeof Routes[structure.controller_name][structure.action_name] === "object"){
			view = Routes[structure.controller_name][structure.action_name];
			let action = view;
			let limit_params = turpial.un(action._limit_params, true);
			
			if(structure.parameters.length > 0){
				var errorParams = false;
				structure.parameters.map(( param )=>{
					if(typeof view[param] !== "undefined"){
						view = view[param];
					}else{
						errorParams = true;
					}
				})
				
				if(limit_params === true && errorParams === true){
					return Routes.errors["404"]._render();
				}
			}


		}else{
			if(structure.action_name === ""){
				view = Routes[structure.controller_name];
			}
		}
	}

	if(return_layout === true){		
		props = view.props ?? {};
	}
	
	
	let has_parameter = false;
	let in_position = false;
	let has_invalid_parameter = false;
	let view_params = view.params;
	if(typeof view_params !== "undefined" && structure.parameters.length !== 0){	
		Object.keys(view_params).map(( routes, key )=>{
			
			has_parameter = true;
			
			let route_splitted = routes.split("/");
			
			if(route_splitted.length === structure.parameters.length){
				props.parameters = [];
				route_splitted.map(function( item, key ){
					
					let is_parameter = item.search("{") >= 0 ? true : false;
					let route = item.split("{").join("").split("}").join("");
					
					if(is_parameter === false){
						if(structure.parameters[key] !== item){
							props.parameters = [];
							has_invalid_parameter = true;
							return;
						}
					}

					in_position = true;
					view = Object.values(view_params)[structure.parameters.length-1];
					
					
					props.parameters[route] = structure.parameters[key];

				})	
					
			}
			

			
			
			
		})
	}else{
		props.parameters = [];
	}

	

	
	RouterStore.update(props)
	if(return_layout === true){
		if(typeof view.template !== "undefined"){
			layout = view.template;
		}
		return layout;
	}


	if(has_parameter === true && in_position === false && has_invalid_parameter === true){
		view = null;
	}

	if(view === null){
		return Routes.errors["404"]._render();
	}

	
	return view._render();
}




layout = showView(CustomRoute, true);
</script>
<svelte:component props={props} this="{layout}" >
	{#if CustomRoute !== false}
		{#await showView(CustomRoute)}
			<Loader status="show" />
			{:then module}
			{#if typeof module !== "undefined" && module !== false && module !== null}
					<!--<RouterEvent event="success" controller={$page.controller_name} />-->
						<svelte:component this={module.default}/>
						<Loader status="hide" />
			{/if}
		{/await}
		{:else}
		{#await showView($page)}
			<Loader status="show" />
			{:then module}
			{#if typeof module !== "undefined" && module !== false && module !== null}
					<!--<RouterEvent event="success" controller={$page.controller_name} />-->
						<svelte:component this={module.default}/>
						<Loader status="hide" />
			{/if}
		{/await}
	{/if}
</svelte:component>


