<script>
import LocalFilter from './LocalFilter.svelte';
import ComponentsGroup from '../ComponentsGroup.svelte';
import Dropdowns from '../dropdowns.svelte';
import { CustomStore } from '../../libraries/customStore/CustomStore';
export let on_change = ()=>{};
export let selected = {
    name: "Colombia",
    value: "COL"
}

let CountriesDropdown = new CustomStore(
    {
        countiresData: [],
        resultSeach: [],
    },
    function(){
        CountriesDropdown = CountriesDropdown;
    }
)

fetch(`https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@countries-c3/countries.json`)
.then((r)=>{
    return r.json();
}).then((r)=>{
    getCountriesArray(r);
})

function getCountriesArray(countiresData) {
    let processArray = [];
    for (const value of countiresData) {
        processArray.push({
            name: value.name,
            value: value.c3
        })
    }
    CountriesDropdown.setJson("countiresData", processArray);
    CountriesDropdown.setJson("resultSeach", processArray)
}

</script>
<style>
</style>

<ComponentsGroup>
    <Dropdowns CustoStoreData={CountriesDropdown} isSearch={true}  label="" on_change={on_change} list={CountriesDropdown.data.resultSeach}>
        {selected.name}
    </Dropdowns>
</ComponentsGroup>

