import {turpial} from "./turpial"
const materialize = function(callback){
    if(typeof M !== "undefined"){
        callback();
        return;
    }
    turpial.fetch({
        url: ["https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0-beta/js/materialize.min.js"],
        ready: ()=>{
            callback();
        },
    })
}

export { materialize }