
class ApplicationInstaller 
{
    /**
     * class to save application data
     * @param {Store} ApplicationData
     * @param {string} application_name
     * @param {string} install
     * @param {json} data
     */
    constructor({ApplicationData, application_name, install, data, callback})
    {
        data = data || {};
        
        install = install === true ? "install" : "uninstall";

        callback = callback || function(){};

        fetch(`${ApplicationData.api_host}/public/api/v1/services/websites/${install}/${application_name}`, {
            headers: {
                'X-Token-Website': ApplicationData.token_website,
                'X-User-Token': ApplicationData.user.public_key,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
            method: "POST",
        })
        .then(( r )=>{
            return r.json();
        }).then(( data )=>{
            callback(data);            
        })
    }
}

export {ApplicationInstaller}