<script>
import { ApplicationData } from './../../data.application.route.js';
import { onMount } from 'svelte';
import DateDropdowns from '../../components/Dropdowns/DateDropdowns.svelte';
import Calendar3Pod from './../../Calendar3Pod.svelte';
import { InlineRoute } from './../../inline.routes.js';
import { page } from '../../turpial.route.store.js';
export let paginatorData = {};
export let list_application_fetch = {};
export let setStatus = null;
let path = "";
let openOutside;
let personalizer = null;
let personalizerDates = [];

let dateValue;


let getOpen = (app)=> {
    openOutside = app;
}

let getData = (data)=>{
    if(data){
        dateValue = data;
    }
    if (personalizer) {
        personalizerDates = dateValue.split(" - ");
        paginatorData.update((object)=>{
            object.conditions.from = Number.parseInt(new Date(personalizerDates[0]).getTime()  / 1000);
            object.conditions.until = Number.parseInt(new Date(personalizerDates[1]).getTime()  / 1000);
            return object; 
        })
        list_application_fetch.fetch();
        if (setStatus != null) {
            setStatus("pending");
            setStatus("completed");
        }
    }
    personalizer = false;
}


function openDatePicker() {
    personalizer = true;
    openOutside.step = 1;
    openOutside.open(openOutside.fromInput);
}
function on_change(name, value, event){
    /* console.log(name, value); */
    if (value === "personalizado") {
        openDatePicker();
    }else if (value === "todo") {
        paginatorData.update((object)=>{
            object.conditions.from = "";
            object.conditions.until = "";
            return object; 
        })
        list_application_fetch.fetch();
        if (setStatus != null) {
            setStatus("pending");
            setStatus("completed");
        }
    }else{
        let from_date = Number.parseInt((new Date(new Date().setDate(new Date().getDate() - value)).getTime()) / 1000);
        let until_date = Number.parseInt(new Date().getTime()  / 1000);

        paginatorData.update((object)=>{
            object.conditions.from = from_date;
            object.conditions.until = until_date;
            return object; 
        })
        list_application_fetch.fetch();
        if (setStatus != null) {
            setStatus("pending");
            setStatus("completed");
        }
    }
}
</script>
<style>
.has-text-gray{
    color: #415B76;
}

.date-value{
    position: absolute;
    right: 0;
    top: 48px;
    cursor: pointer;
    z-index: 5;
}

.flex-column-center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.background-gray{
    background-color: #F5F8FA;
    border-radius: 20px;
}

</style>
{#if $InlineRoute.path === "projects"}
    <div class="columns is-multiline background-gray">
        <div class="column is-4">
            <img src="https://ywzxemluhxrzjvojqjld.supabase.co/storage/v1/object/sign/tripobucket/3Pod_Img_Apps.svg?token=eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1cmwiOiJ0cmlwb2J1Y2tldC8zUG9kX0ltZ19BcHBzLnN2ZyIsImlhdCI6MTY1MzU3MTI1NCwiZXhwIjoxOTY4OTMxMjU0fQ.adyBomhAMhsRrqxUmVRj-dCQDH7mn7pUxp50Xk_JlCc" alt="integrations-img">
        </div>
        <div class="column is-8 flex-column-center">
            <span class="is-size-4 has-text-gray fw600">¡Instala aplicaciones para hacer crecer tu negocio!</span>
            <p class="mt-4 has-text-gray">Con ellas podrás automatizar procesos como la gestión de facturación, cotización, inventario, envios, contraentrega, dropshipping y mucho más.</p>
            <button class="transparent-3pod is-rounded mt-4" on:click="{()=>{ page.route("projects/applications") }}" type="button">Instalar aplicaciones</button>
        </div>
    </div>
{/if}
<!--
    <div data-type="datetime" data-required="true"  data-twelve-hour="true" data-name="date-input" data-class="formElements" class="calendar-3pod">
-->

<!-- <button on:click={() => openOutside.open(openOutside.fromInput)}>Mostrar fecha</button> -->

<div class="flex-center-space-between is-relative">
    <span class="has-text-gray fw600 is-size-4">
        {$ApplicationData.website_title}
    </span>
    <DateDropdowns on_change={on_change}/> 
    {#if dateValue}
        <span class="date-value has-text-gray is-size-7" on:click={()=>{openDatePicker()}}>{dateValue}</span>
    {/if}
</div>

<Calendar3Pod props="{{
    visivility: "hidden",
    type: "date",
    twelveHour: "true",
    name: "dates",
    "class": "formsElements",
    "isRange": "true",
}}" getOpen={getOpen} getData={getData}/>
