<script>
import AppForms from "../../libraries/coreApp/AppForms";


export let items = {};

let searchData;

function filterData() {
    let data = AppForms.retrieve({
        form: searchData,
        targets: ".formsElements"
    })
    if(data){
        let results = items.data.countiresData.filter((item) => {
            if(item.name.toLowerCase().includes(data.toSearch.toLowerCase())){
                return item;
            }
        });
        items.setJson("resultSeach", results);
    }else{
        items.setJson("resultSeach", items.data.countiresData);
    }
}

</script>

<form bind:this={searchData} class="search-container" on:input|preventDefault={()=>{filterData()}}>
    <input type="text" class="search-input formsElements" name="toSearch"  placeholder="Buscar">
    <span class="icon">
        <i style="font-size: 20px;" class="icon-tripod-search"></i>
    </span>
</form>

<style>
    .search-container{
        position: relative;
    }

    .search-input{
        border: 1px solid #E1E8ED;
        border-radius: 20px;
        opacity: 1;
        outline: none;
        width: 160px;
        height: 32px;
    }

    .search-input::placeholder{
        color: #E1E8ED;
        font: normal normal normal 14px/20px Montserrat;
        opacity: 1;
        width: 49px;
        height: 18px;
        transform: translateX(8px);
    }

    .icon{
        position: absolute;
        display: block;
        top: 5px;
        right: 9px;
        user-select: none;
    }
</style>