let app_name;
if(IS_ONLINE === false){
    app_name = "websites.3pod.io"
}else{
    app_name = "websites.3pod.io"
}
let server_host = "localhost";
if(typeof IS_ONLINE === "undefined"){
    var IS_ONLINE = false;
}
switch(server_host){
    case "localhost": {
        server_host = "http://localhost/"+app_name+"/";
        break;
    }
    case "localIP": {
        server_host = "http://127.0.0.1/"+app_name+"/";
        break;
    }
    case "production": {
        server_host = "https://"+app_name+"/";
        break;
    }
}


let global_variables = {
    __HOST: __HOST,
    location_host: location_host,

    /**
     * Define the application enviroment
     */
    IS_ONLINE: IS_ONLINE,
    PUBLIC_PATH: PUBLIC_PATH,
    rootStyles: rootStyles,
    topbar: topbar,
    turpial: turpial,
    server: server_host,
    server_api: {
        checkAuth: `${server_host}api/auth/check/`
    }
}


export { global_variables };