<script>
	import { ApplicationData } from './../../../../data.application.route.js';
	import LoaderElement from './../../../../components/Elements/LoaderElement.svelte';
import { page } from './../../../../turpial.route.store.js';
import LinkPrimary from './../../../../components/Buttons/LinkPrimary.svelte';

/* let listData = [
    {
        appImage: "https://i.ibb.co/4NcJWrc/Logo-Efficommerce-H-FINAL-2x.png",
        description: "Importa productos de Dropi y sincroniza tus ordenes.",
        cost: "Gratis",
        activation: true,
        has_switch: false,
        options: "1",
        route: "projects/applications/effi",
    },
] */

let listData = [];
let show = false;

function getApps(){
    fetch(`${$ApplicationData.api_host}/public/api/v1/services/builder/apps`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    })
    .then(( r )=>{
        return r.json();
    }).then(( data )=>{
        listData = data.data
        
        listData = listData.map(( item )=>{
            item.installed = $ApplicationData.website_data.installed_apps[item.brand_id]?.installed ?? false;

            return item;
        });
        console.log(listData)
        show = true;
    })

}

getApps();
function switchController(application, __switch, element) {
    __switch = element.checked === true ? "install" : "uninstall";
    element.setAttribute("disabled", "");

    
    fetch(`${$ApplicationData.api_host}/public/api/v1/services/websites/${__switch}/${application}`, {
        headers: {
            'X-Token-Website': $ApplicationData.token_website,
            'X-User-Token': $ApplicationData.user.public_key
        },
    })
    .then(( r )=>{
        return r.json();
    }).then(( data )=>{
        console.log(data)
        element.removeAttribute("disabled");
    })
}

</script>
<style>
.logo-app{
    max-width: 200px;
    max-height: 60px;
}
.box-container{
    background-color: var(--color-grey-background);
    border-radius: 20px;
    padding: 1.75rem;
}
.description-text{
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 101px;
}
.description-text p{
    text-overflow: ellipsis;
}
</style>

{#if show}
    <ul class="overflow-container full-size">
        {#each listData as item}
            {#if item.is_available}
                <li>
                    <section class="columns is-multiline box-container is-align-items-center">
                        <div class="column">
                            <img class="logo-app" src={item.logos[0]} alt="logo">
                        </div>
                        <div class="description-text column">
                            <p class="fw500 has-text-gray">
                                {item.description}
                            </p>
                        </div>
                        <div class="column has-text-centered">
                            <div class="columns is-multiline is-mobile">
                                    <div class="column">
                                        <div class="switch-container flex-center">
                                            <p style="margin-right: 1rem;">{item.installed === true ? "Activa" : "No instalada"}</p>
                                            <div class="field" style="margin-bottom: 0;">
                                                <input id={item.id} type="checkbox" name={item.id} class="switch is-rounded" checked={item.installed} on:change={function(e){
                                                    switchController(item.brand_id, item.installed, e.target)
                                                }}>
                                                <label for={item.id}></label>
                                            </div>
                                        </div>
                                    </div>                  
        <!--                         <div class="column">
                                    <button type="button" class="button is-light is-small has-text-success fw500">
                                        Gratis
                                    </button>
                                </div> -->
                            </div>
                        </div>                
                        <div class="column has-text-centered">
                            <button class="button is-primary brand is-small is-rounded" on:click="{()=>{ page.route("projects/applications/effi") }}" type="button">Configurar</button>
                        </div>
                    </section>
                    <div class="column is-2">
                        
                    </div>
                </li>
            {/if}
        {/each}
    </ul>
    {:else}
        <LoaderElement style_container="min-height:400px;" />
{/if}
