<script>
import { RouterStore } from './../../../RouterStore.js';
import LoaderElement from './../../../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../../../data.application.route.js';
import { page } from '../../../turpial.route.store.js';


let order = {};
let subtotal = 0;
let quantity_total = 0;

fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/orders/${$RouterStore.parameters.order}`, {
    headers: {'X-Token-Website': $ApplicationData.token_website}
})
.then((r)=>{
    return r.json();
}).then((r)=>{
    order = r;
    console.log(order);
    get_totals();
})


function get_totals(){
    order.items.map(( item )=>{
        subtotal = subtotal + item.total

        quantity_total = quantity_total + item.quantity;
    })
}


</script>
{#if typeof order.id !== "undefined"}
    <div class="is-line flex-center-flex-start mb-2">
        <a href="#path:/projects/sales_report" class="flex-center-flex-start">
            <i class="icon-tripod-arrow-back is-size-2"></i>
            <p style="margin-left: 1rem;">volver</p>
        </a>
    </div>
    <section class="container-card has-text-gray w-100">
        <article class="card-item bg-gray px-5 py-4">
            <div class="flex-center-space-between is-size-5">
                <span class="fw700">Estado</span>
                <div class="flex-center" style="gap: 6px;">
                        {#if order.status === "pending"}
                            <i class="icon-tripod-package" style="color: var(--color-primary);"></i>
                        {:else if  order.status === "canceled"}
                            <i class="icon-tripod-warning" style="color: var(--color-danger);"></i>
                        {:else if order.status === "completed"}
                            <i class="icon-tripod-shipped" style="color: var(--color-success);"></i>
                        {:else if order.status === "shipped"}
                            <i class="icon-tripod-truck-moving" style="color: var(--color-primary);"></i>
                        {/if}
                    <span class="is-size-6 fw500">
                        {#if order.status === "pending"}
                             <span style="color: var(--color-primary);">En preparación</span>
                        {:else if  order.status === "canceled"}
                             <span style="color: var(--color-danger);">Cancelado</span>
                        {:else if order.status === "completed"}
                            <span style="color: var(--color-success);">Entregado</span>
                        {:else if order.status === "shipped"}
                            <span style="color: var(--color-primary);">En camino</span>
                        {/if}
                    </span>
                </div>
            </div>
            <hr>
            <div>
                <p class="fw700 is-size-5">Detalles</p>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Nombre</span>
                    <span class="name-info">{order.customer_name}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Correo</span>
                    <span>{order.customer_email}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Teléfono</span>
                    <span>{order.shipping_address["phone"]}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Metodo de pago</span>
                    <span>{order.payment_method}</span>
                </div>
            </div>
            <hr>
            <div>
                <p class="fw700 is-size-5">Datos de envío</p>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">País / región</span>
                    <span>{order.billing_address["country"]}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Provincia</span>
                    <span>{order.billing_address["address"]}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Ciudad</span>
                    <span>{order.billing_address["city"]}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600">Adicional</span>
                    <span>{order.billing_address["address"]}</span>
                </div>
            </div>
        </article>
        <article class="card-item px-5 py-4">
            <div class="flex-center-flex-end" style="gap: 2rem;">
                <a href="#">Editar orden</a>
                <button type="button" class="button brand is-primary is-rounded" on:click={() => page.route(`/projects/sales_report/${order.invoice_no}/process`)}>
                    Procesar orden
                </button>
            </div>
            <div>
                <div>
                    <span class="fw700 is-size-5">Productos</span>
                    <span>
                        (Total
                            <span class="fw600">{order.items.length}</span>)
                    </span>
                </div>    
            </div>
            <div class="bg-gray px-5 py-4 flex-center-space-between" style="flex-direction: column; gap: 1rem;">
                {#each order.items as products }
                    <div class="flex-center-space-between w-100" style="gap: 1rem">
                        <div class="img-container has-shadow">
                            <img src="{products.images}" alt="product-item">
                        </div>
                        <div class="w-80">
                            <div class="product-info">
                                <span class="fw600">{products.name}</span>
                                <span>
                                    x{products.quantity === 1 
                                    ? `${products.quantity} unidad` 
                                    : `${products.quantity} unidades`}
                                </span>
                            </div>
                            <div class="flex-center-space-between mt-3">
                                <div class="flex-center" style="gap: 8px;">
                                    <i class="icon-tripod-package" style="color: #415B76"></i>
                                    <span class="fw500">Producto físico</span>
                                </div>
                                <span class="fw500">$ {products.total}</span>
                            </div>
                        </div>
                    </div>        
                {/each}
                
            </div>
            <div class="px-5 py-4 fw500">
                <div class="paid-info">
                    <span class="fw500">Pagado</span>
                    <span class="fw500">
                        {order.paid === false ? 'No pagado' : 'Pagado'}
                    </span>
                </div>
                <div class="flex-center-space-between mt-5">
                    <div class="units-info">
                        <span>Subtotal</span>
                        <span>x{quantity_total} unidades</span>
                    </div>
                    <span>$ {subtotal}</span>
                </div>
                <div class="flex-center-space-between mt-3">
                    <span>Gastos de envío</span>
                    <span>$ {order.shipping_amount}</span>
                </div>
            </div>
            <hr>
            <div class="px-5 flex-center-space-between">
                <span class="fw500">Total</span>
                <span class="fw600">${order.total} {order.currency}</span>
            </div>
            <div class="bg-gray px-5 py-4 mt-5">
                <div class="flex-center-space-between w-100">
                    <span class="fw700">Tramitación de pedido</span>
                    <div class="tag-float">
                        <span class="is-float" style="left: -111px;">
                            <p class="is-size-7 has-text-gray fw500">Esta orden esta conectada con Efficommerce.</p>
                        </span>
                        <img src="https://i.ibb.co/pX5fmw9/maxresdefault.jpg" alt="effi-icon" class="effi-icon">
                    </div>
                </div>
                <div class="flex-center-space-between w-100 mt-4">
                    <span class="fw600">Enviado a través de</span>
                </div>
                <div class="flex-center-space-between w-100 mt-4">
                    <span class="fw600">Número de guía</span>
                </div>
            </div>
        </article>
    </section> 
    {:else}
    <LoaderElement style_container="min-height:400px;" />
{/if}


<style>

    .bg-gray{
        background-color: #F5F8FA;
    }

    .has-text-gray{
        color: #415B76;
    }

    .container-card{
        display: flex;
        flex-direction: row;
        gap: 30px;
    }

    .container-card .card-item{
        border-radius: 20px;
    }

    .container-card .card-item:nth-child(1){
        width: 40%;
    }
    .container-card .card-item:nth-child(2){
        width: 60%;
    }

    .container-card hr{
        background-color: #B4C0C9;
        height: 1px;
        margin-top: 1.5rem;
    }

    .w-100{
        width: 100%;
    }

    .w-80{
        width: 80%;
    }

    .product-info, .units-info, .paid-info{
        display: flex;
        align-items: center;
        gap: 26px;
    }

    .paid-info span:nth-child(2){
        background-color: rgba(180,192,201,0.25);
        padding: 2px 13px;
        border-radius: 18px;
    }

    .name-info{
        text-transform: lowercase;
    }

    .img-container{
        min-width: 77px; 
        min-height: 77px;
        max-width: 77px;
        max-height: 77px;
    }

    .effi-icon{
        width: 44px;
        height: 34px;
        object-fit: cover;
    }

    .is-line{
        width: 100%;
    }
    .is-line a{
        text-decoration: none;
        color: #415B76;
    }

    @media only screen and (max-width: 1024px){
        .container-card{
            display: flex;
            flex-direction: column;
            gap: 30px;
        }
        .container-card .card-item:nth-child(1){
            width: 100%;
        }
        .container-card .card-item:nth-child(2){
            width: 100%;
        }
    }
    
</style>