<script>
import Table from "../../../../components/Table/Table.svelte";
import { EmailRegex } from "../../../../libraries/EmailRegex";
export let paginatorData;

let regexEmailValidate = [];
$: {
    if(paginatorData){
        console.log(paginatorData?.data?.listData?.data)
        paginatorData?.data?.listData?.data.map((value) => {
            value.fields.map((field) => {
                let isAEmail = EmailRegex.test(field.value);
                if(isAEmail){
                    regexEmailValidate = [...regexEmailValidate, field.value];
                }
            })
        })
    }
}

</script>
<style>
.w-100{
    width: 100%;
}
.list-item-table{
    font-size:1.125rem;
    text-align: left;
} 
/* .link-item{
    font-size: 1rem;
    font-weight: 500;
    color: #296FFF;
} */
</style>

<Table listHeader={['Nombre','Fecha','Acciones']}>
    {#if paginatorData?.data?.listData?.data?.length > 0}
        {#each paginatorData?.data?.listData?.data as data, index }
            <tr class="w-100">
                <td class="list-item-table">{data.fields[0].value}</td>
                <td class="list-item-table">{data.created_at}</td>
                <td class="list-item-table">
                    <a href="#path:/projects/forms_report/{data.id}">
                        Ver más detalles
                    </a>
                </td>
            </tr>   
        {/each}
    {/if}
</Table>
