function parsePaginatorData(paginator, current){
    if (paginator.type === "supabase") {
        let navArray = [];
        for (let i = 1; i < paginator.navigation[paginator.navigation.length - 1] + 1; i++) {
            navArray.push(i);
        }        
        paginator.selfPaginator = {
            dataList: {
                data: paginator.data,
                navigation: navArray,
                limit: navArray[navArray.length - 1]
            },
            pos: paginator.perpage * current - paginator.perpage,
            position: current,
            searchFor: "",
            fromDate: "",
            toDate: "",
            type: paginator.type,
        }
    }else if (paginator.type === "default") {
        paginator.selfPaginator = {
            dataList: {
                data: paginator.dataList.data,
                navigation: paginator.dataList.navigation,
                limit: paginator.dataList.limit,
            },
            pos: paginator.position,
            position: current,
            searchFor: false,
            fromDate: "",
            toDate: "",
            type: paginator.type,
        }
    }
    return paginator;
}
 export {parsePaginatorData}