<script>
	import { ApplicationData } from './../../../../data.application.route.js';
import { turpial } from '../../../../libraries/turpial';

import { RouterStore } from '../../../../RouterStore';
import MediaQuery from './../../../../components/MediaQuery.svelte';
export let type = "default";
export let saveButton = true;
export let steps;
export let logoUrl;
export let topInstructions = "none";
export let bottomInstructions = "none";
export let underLineText = "none";
export let FormDataApplication;
export let on_save = function(){};
export let application_name = "";


let id_elements = "rd-"+turpial.random_string(6);

function switchController(element) {
    FormDataApplication.update(( data )=>{
        data.form.install = element.checked;
        return data;
    })
}

let container = null;
FormDataApplication.on_update((data)=>{
    let buttons = [...container.querySelectorAll(".save-button")];
    buttons.map(( button )=>{
        button.removeAttribute("disabled");
    });
})


let application = $ApplicationData.website_data?.installed_apps[application_name] ?? null;

</script>
{#if application !== null}
    <section class="first-container full-size flex-column" bind:this="{container}">
        <div class="top-container full-size flex-center-space-between">
            <div class="logo-container">
                <img src={logoUrl} alt="Logo">
            </div>
            <div class="actions-container flex-center-space-between">
                {#if underLineText != "none"}
                    <a class="underline-text" href="#path:/projects/applications/{$RouterStore.parameters.application}/products">{underLineText}</a>
                {/if}
                <div class="switch-container flex-center">
                    <p style="margin-right: 1rem;">{application?.installed ? "Activo" : ""}</p>
                    <div class="field" style="margin-bottom: 0;">
                        <input id={id_elements+"-installer"} type="checkbox" name="install"  class="switch is-rounded" checked={application.installed} on:change={function(e){switchController(e.target)}}>
                        <label for={id_elements+"-installer"}></label>
                    </div>
                </div>
                <MediaQuery query="(min-width: 900px)">
                    {#if saveButton}
                        <button class="save-button button is-rounded" on:click="{function(e){ on_save(FormDataApplication.data.form, e.target) }}" disabled>Guardar</button>
                    {/if}
                </MediaQuery>
            </div>
        </div>
        <div class="bottom-container full-size {type}">
            {#if type == "only-top"}
                <slot><!-- optional fallback --></slot>
            {:else}
                <div class="slot-container">
                    <slot><!-- optional fallback --></slot>
                    <MediaQuery query="(max-width: 900px)">
                        {#if saveButton}
                            <button class="save-button button is-rounded" on:click="{function(e){ on_save(FormDataApplication.data.form, e.target) }}" disabled>Guardar</button>
                        {/if}
                    </MediaQuery>
                </div>
                <div class="steps-container">
                    <ul class="full-size">
                        <li class="fw600" style="font-size: 20px; margin-bottom: 1rem;">Instruciones</li>
                        {#if topInstructions != "none"}
                            <li style="padding-bottom: 1rem; word-break: break-word;">{topInstructions}</li>
                        {/if}
                        {#each steps as step, index}
                            <li class="step-item">
                                <div class="step-tittle fw600">
                                    <p style="white-space: nowrap;">Paso {index + 1}</p>
                                </div>
                                <div class="step-description">
                                    <p style="word-break: break-word;">{step}</p>
                                </div>
                            </li>
                        {/each}
                        {#if bottomInstructions != "none"}
                            <li style="padding-top: 1rem; word-break: break-word;">{bottomInstructions}</li>
                        {/if}
                    </ul>
                </div>
            {/if}
        </div>
    </section>
{/if}

<style> 
.first-container{
    min-height: 300px;
    color: #415B76;
}
.top-container{
    height: 90px;
    border-bottom: solid #35465b44 1px;
    padding-bottom: 1rem;
}
.top-container .underline-text{
    position: relative;
    text-decoration: underline;
    margin-left: auto;
    margin-right: 2rem;
}
.save-button{
    background: #296FFF;
    color: white;
    border: none;
}
.top-container .logo-container{
    display: flex;
    height: 100%;
    padding: 0 0 1rem 0;
}
.top-container .logo-container img{
    height: 100%;
}
.bottom-container{
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
}
.bottom-container .slot-container{
    width: 65%;
    padding-right: 1rem;
    display: flex;
    flex-direction: column;
}
.bottom-container .steps-container{
    width: 35%;
    min-height: 400px;
}
.bottom-container .steps-container ul{
    width: 100%;
    min-height: 200px;
    background: var(--color-grey-background);
    border-radius: 20px;
    padding: 1rem;
}
.bottom-container .steps-container ul .step-item{
    display: flex;
    flex-direction: row;
    width: 100%;
    font-size: 16px;
    padding: 0.55rem 0;
}
.bottom-container .steps-container ul .step-item .step-tittle{
    margin-right: 1rem;
}
@media only screen and (max-width: 900px){
    .bottom-container.default{
        flex-direction: column-reverse;
    }
    .bottom-container.default .steps-container{
        width: 100%;
    }
    .bottom-container .slot-container{
        margin-top: 1rem;
        width: 100%;
        padding-right: 0;
    }
    .top-container .underline-text{
        position: relative;
    }
    .save-button{
        margin-left: auto;
    }
}
@media only screen and (max-width: 750px){
    .top-container{
        flex-direction: column;
        height: 120px;
    }
    .top-container .logo-container{
        height: 70%;
        padding: 0 0 0.55rem 0;
        margin-left: 0;
        margin-right: auto;
    }
    .top-container .actions-container{
        margin-left: auto;
        margin-right: 0;
    }
}
</style>
