<script>
import Materialize from './components/Materialize.svelte';
import { materialize } from "./libraries/materialize";
import { calendari18n } from "./libraries/calendari18n";
import { onMount } from 'svelte';
import { clickOutside } from './libraries/clickOutside';
export let props = null;
export let getOpen = ()=>{};
export let getData = ()=>{};

/**
 * options:
 * - data-type: date, datetime or time
 * - data-format: date format (e.g yyyy-mm-dd)
 * - data-placeholder: string
 * - data-isRange: true/false
 * - data-twelveHour: true/false
 * - data-mindate: type new Date()
 * - data-maxdate: type new Date()
 * - data-inputClasses: classes for all inputs
 * - data-class: classes for input-results
 * Place any of this options in container div.calendar-3pod
 */

let materialize_loaded = false;
let pickadateElement;
let container;

let calendar = props ?? $$props.calendar;
let all_props = $$props;
let number_calendar = all_props.number_calendar;
let placeholder = props.placeholder ?? calendar.dataset?.placeholder ?? "yyyy-mm-dd";

/**
 * Formato
 */
let format = props.format ?? calendar.dataset?.format ?? "yyyy-mm-dd";


/**
 * Visibilidad de los inputs
 */
let visivility =  props.visivility ?? calendar.dataset?.visivility ?? "hidden";
if(visivility === "hidden"){
    visivility = "opacity:0;visibility:hidden;"
}

/**
 * date para solo fecha
 * datetime para seleccionar fecha y hora
 * time para solo fecha
 */
let type = props.type ?? calendar.dataset?.type ?? "date"; // date, datetime, time

/**
 * Si es un rango de fechas
 */
let isRange = props.isRange ?? calendar.dataset?.isRange ?? "false";

/**
 * Si es true Formato de hora en 12AM - 12PM
 */
let twelveHour = props.twelveHour ?? calendar.dataset?.twelveHour ?? "false";



let inputClasses = props.inputClasses ?? calendar.dataset?.inputClasses ?? "";

/**
 * 
 */
let classes = props.class ?? calendar.dataset?.class ?? "";


/**
 * Minimo
 */
let mindate = props.mindate ?? calendar.dataset?.mindate ?? null;

/**
 * Máximo
 */
let maxdate = props.maxdate ?? calendar.dataset?.maxdate ?? null;
let nameInputResult = props.name ?? calendar.dataset?.name ?? "date";


/**
 * Validación en formulario
 */
let required = props.required ?? calendar.dataset?.required ?? "false";
let id_component = props.idComponent ?? calendar.dataset?.idComponent ?? "component-"+ turpial.random_string(8);
let inputResult;

required = required == "true" ? true : false;
twelveHour = twelveHour == "true" ? true : false;
isRange = isRange == "true" ? true : false;

/**
 * Init materialize
*/


$: {
    if(inputResult){
        getData(inputResult.value)
    }
}
onMount(()=>{   
    let container = document.getElementById(id_component);
    if(container === null){ return; }
    let inputs = [...container.querySelectorAll(".results-calendar-input")];
    inputs.map(( input )=>{
        if(required === true){
            input.setAttribute("required", '');
        }else{
            input.setAttribute('readonly', '');
        }
    })

    
    materialize(()=>{
        materialize_loaded = true;
        let app = new Calendar(container);

        // app.open(app.fromInput);

        getOpen(app);
    })
})

class inputsCalendar 
{
    constructor()
    {
        this.container = document.getElementById(id_component);
    }
    hiddenInputs()
    {
        let inputs = [...this.container.querySelectorAll(".hidden-calendar-input")];
        let status = "filled";
        inputs.map(( input )=>{
            if(input.value === ""){
                status = "empty";
            }
        })

        return {
            restore: function(){
                inputs.map(( input )=>{
                    input.value = "";
                })
            },
            status: status,
        }
    }
    /**
     * calendarInputs:
     * - muestra si los inputs visibles estan llenos o no.
     */
    calendarInputs()
    {
        let inputs = [...this.container.querySelectorAll(".results-calendar-input")];
        let status = "filled";
        inputs.map(( input )=>{
            if(input.value === ""){
                status = "empty";
            }
        })

        return {
            restore: function(){
                inputs.map(( input )=>{
                    input.value = "";
                })
            },
            status: status,
        }
    }
}

function restoreAll()
{
    let inputs = new inputsCalendar();
    inputs.calendarInputs().restore();
    inputs.hiddenInputs().restore();
}

class Calendar
{
    
    constructor(container){
        if(materialize_loaded === false){
            return;
        }
        if(container === null){ return; }
        this.container = container;
        this.superContainer = container.parentNode;
        this.controls = container.querySelector(".container-controls");
        this.fromInput = container.querySelector(".from-calendar");        
        this.toInput = container.querySelector(".to-calendar");
        this.toInputTime = container.querySelector(".to-calendar-time");
        this.fromInputTime = container.querySelector(".from-calendar-time");
        this.resultsInputFrom = container.querySelector(".results-calendar-from");
        this.resultsInputTo = container.querySelector(".results-calendar-to");
        this.is_open = false;
        this.currentInstance = null;
        this.window_open = "date";
        this.type_close = "default";
        this.results = [];
        this.step = 1;
        this.is_opening = false;

        this.events();
    }
    destroyInstance()
    {
        if(this.currentInstance !== null){
            this.currentInstance.destroy();
        }
    }
    events(){
        let app = this;
        if(!this.controls.classList.contains("control-container-events-added")){
            this.controls.classList.add("control-container-events-added")
            let elements = [...this.controls.querySelectorAll("input, .button")];
            elements.map((node)=>{

                /**
                 * Eventos
                */
                node.addEventListener("click", (event)=>{
                    if(app.is_open === true){ return; }
                    app.step = 1; // restart steps
                    if(this.window_open === "time"){
                        app.open(app.toInputTime);
                        return;
                    }

                    if(app.step === 1){
                        let inputs = new inputsCalendar();
                        inputs.hiddenInputs().restore();
                    }
                    
                    app.open(app.fromInput);
                })
            })
            
        }
        
        

        this.fromInput.addEventListener("change", ()=>{
            this.resultsInputFrom.value = this.fromInput.value;
            app.destroyInstance();
        })
        if(this.fromInputTime !== null){
            this.fromInputTime.addEventListener("change", ()=>{
                this.resultsInputFrom.value = `${this.resultsInputFrom.value} : ${this.fromInputTime.value}`;
                inputResult.value = `${this.resultsInputFrom.value}`;
                app.onClose();
            })
        }
        
        if(this.toInput !== null){
            this.toInput.addEventListener("change", ()=>{
                this.resultsInputTo.value = this.toInput.value;
                inputResult.value = `${this.resultsInputFrom.value} - ${this.resultsInputTo.value}`;
            })
        }
        if(this.toInputTime !== null){
            this.toInputTime.addEventListener("change", ()=>{
                this.resultsInputTo.value = `${this.resultsInputTo.value} : ${this.toInputTime.value}`;
                getData.value = `${this.resultsInputFrom.value} - ${this.resultsInputTo.value}`;
                app.onClose();
            })
        }

        /**
         * Añadir resultados a unúnico input.
        */
       

    }
    cancelOnClickOutside()
    {
        let app = this;
        if(!this.container.classList.contains("event-click-outside")){
            this.container.classList.add("event-click-outside")
            let yearList = "ul[id*='select-options-'].select-dropdown";

            clickOutside([this.container, yearList], (destory)=>{
                this.container.classList.remove("event-click-outside")
                app.is_open = false;
                setTimeout(() => {
                    app.type_close = "complete";
                    app.currentInstance.close();
                    setTimeout(() => {
                        try {
                            app.currentInstance.destroy();
                            
                        } catch (error) {
                            console.warn(error)
                        }
                        app.type_close = "default";
                    }, 350);
                    /**
                     * in case of range
                    */
                    let inputs = new inputsCalendar();
                    if(inputs.hiddenInputs().status === "empty"){
                        inputs.calendarInputs().restore();
                        inputs.hiddenInputs().restore();
                    }      
                    destory();   
                }, 100);
            }, true)
        }
        
        
    }
    open(input, method){        
        let app = this;        
        method = method || "Datepicker"
        this.results = [];
        if(app.is_open === true){ return; }
        if(app.is_opening === true){ return; }
        app.is_opening = true;
        app.is_open = true;        
        let options = {
            container: `#${id_component} .pickadate-${method}`,
            format: format,
            i18n: {
                months: calendari18n.months,
                monthsShort: calendari18n.monthsShort,
                weekdays: calendari18n.weekdays,
                weekdaysShort: calendari18n.weekdaysShort,
                weekdaysAbbrev: calendari18n.weekdaysAbbrev
            },
            onClose: ()=>{
                app.onClose();
            },
        }
        
        if(mindate !== null){
            options.minDate = new Date(new Date(mindate).getTime()+1000);
        }
        if(maxdate !== null){
            options.maxDate = new Date(new Date(maxdate).getTime()+1000);
        }
       

        

        if(method === "Timepicker"){
            options.twelveHour = twelveHour;
            options.i18n = {
                cancel: "Cancelar",
                done: "Ok"
            }
        }else{
            function applyMinDate(){
                let day = (3600 * 24) * 1000;
               return new Date(new Date(app.resultsInputFrom.value).getTime()+day);
            }
            if(app.step === 2 && type === "datetime" && isRange === true){
                options.minDate = applyMinDate();
            }
            if(app.step === 2 && type === "date" && isRange === true){
                options.minDate = applyMinDate();
            }
        }
        
        
        let instance = M[method].init(input, options);
        app.currentInstance = instance;


        setTimeout(() => {
            instance.open()
            if(method === "Datepicker"){
                if(input.value !== ""){
                    instance.setDate(new Date(input.value));
                }else{
                    instance.setDate(new Date());
                }
            }

            if(method === "TimePicker"){
                if(input.value !== ""){
                    instance.defaultTime(input.value);
                }else{
                    instance.defaultTime('now');
                }
            }

            setTimeout(() => {                
                app.cancelOnClickOutside();
                app.is_opening = false;
            }, 100);
        }, 100);
    }
    onClose(){
        let app = this;
        
        if(app.type_close === "complete"){
            return;
        }
        setTimeout(()=>{
            app.is_open = false;
            if(type === "datetime" && app.step === 1){                        
                app.open(app.fromInputTime, "Timepicker");
                app.step++;
            }else if(type === "datetime" && isRange === true && app.step === 2){
                app.open(app.toInput);
                app.step++;
            }else if(type === "datetime" && isRange === true && app.step === 3){
                app.open(app.toInputTime, "Timepicker");
                app.step++;
                return;
            }else if(type === "date" && isRange === true && app.step === 1){
                app.step++;
                app.open(app.toInput);
                return;
            }
            
        }, 250)
    }
}




</script>
<Materialize />
<style>
.datepicker-materialize{
    font-family: 'Montserrat',"Roboto","Calibri", sans-serif;;
}
.datepicker-materialize :global(.modal-content){
    width: auto;
}
.input-calendar:focus{
    box-shadow: none !important;
}
.datepicker-materialize :global(.is-range-true .results-calendar-from){
    border-right: 0;
    border-left: 0;
}
.datepicker-materialize :global(.results-calendar-from){
    border-left: 0;
}
.datepicker-materialize :global(.is-range-true .results-calendar-to){
    border-left: 0;
}
.datepicker-materialize :global(.datepicker-calendar-container){
    background: #fff;
    border-radius: 0 0 20px 20px;
}
.datepicker-materialize :global(.confirmation-btns button:first-child){
    display: none;
}
.datepicker-materialize .button-icon{
    border-right: none;
}
.datepicker-materialize .button-icon:hover{
    background-color: #fff !important;
}
.datepicker-materialize :global(.modal){
    top: calc(50vh - 200px) !important;
}
.datepicker-materialize :global(.modal){
    top: calc(50vh - 200px) !important;
    max-width: 100% !important;
}

.datepicker-materialize .icon-calendar-3pod::before{
    content: url('data:image/png;base64,');
    
}
.datepicker-materialize .icon-calendar-3pod{
    transform: scale(0.5);
    position: relative;
    top: 3px;
}
.datepicker-materialize .button, .input{
    border-width: 1px;
}
.datepicker-materialize :global(.modal-overlay){
    display: none !important;
}
.datepicker-materialize :global(.datepicker-container), .datepicker-materialize :global(.timepicker-container){
    flex-direction: column !important;
    width: fit-content;
    margin: auto;
}
.datepicker-materialize :global(.timepicker-display-am-pm){
    position: absolute;
    right: 1rem;
    bottom: 1rem;
}
.datepicker-materialize :global(.datepicker-date-display), .datepicker-materialize :global(.timepicker-digital-display){
    padding: 20px 22px;
    flex: 1 auto !important;
    border-radius: 20px 20px 0 0;
    width: 325px;
}
.datepicker-materialize :global(.timepicker-analog-display){
    background: #fff;
    border-radius: 0 0 20px 20px;
}

.container-pickadate{
    width: fit-content;
    max-height: fit-content;
    margin: auto;
    border-radius: 0 0 20px 20px !important;
    position: absolute;
    z-index: 20;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.container-controls input{
    cursor: pointer;
}
.container-controls{
    position: relative;
}
.container-visible-inputs{
    position: relative;
    justify-content: center;
}
.icon-delete{
    position: absolute;
    z-index: 5;
    right: 0.25rem;
    top: 0.45rem;
    padding: 0.8rem 1rem;
}
.datepicker-materialize-container{
    position: relative;
}
.datepicker-materialize :global(.datepicker-modal), .datepicker-materialize :global(.modal){
    position: relative !important;
    bottom: 0;
    top: 0 !important;
    left: 0 !important;
    margin: auto;
    box-shadow: 2px 2px 32px rgb(133 133 133 / 32%);
    border-radius: 20px;
}
.datepicker-materialize input.input, 
.datepicker-materialize button.button,
.datepicker-materialize input.input:hover, 
.datepicker-materialize button.button:hover
{
    border-color: #AAB8C2;
}

:global(.is-range-true) .results-calendar-from{
    background: transparent url('data:image/svg+xml;base64,') no-repeat 96%/10px;
}
</style>
<div id={id_component} bind:this="{container}" class="datepicker-materialize materialize" tabindex="-1" style="height: 1rem;">
    <div class="datepicker-materialize-container" tabindex="-1">
        {#if isRange === true}
            <span class="icon icon-delete" on:click="{restoreAll}" style="{visivility}" >
                <div class="delete has-background-danger"></div>
            </span>
        {/if}
        <div class="container-controls">
            <div class="flex-center" style="margin: 0;">
                <div class="field has-addons">                    
                    <div class="control">
                        <input tabindex="-1" type="hidden" name="from-calendar" class="from-calendar hidden-calendar-input is-hovered {inputClasses} input-calendar input" placeholder="{placeholder}" readonly>   
                        {#if type === "datetime"}
                            <input tabindex="-1" type="hidden" name="from-calendar-time" class="from-calendar-time hidden-calendar-input is-hovered {inputClasses} input-calendar input" placeholder="{placeholder}" readonly>   
                        {/if}                   
                    </div>
                    {#if isRange === true}
                        <div class="control">
                            <input tabindex="-1" type="hidden" name="to-calendar" class="to-calendar hidden-calendar-input is-hovered {inputClasses} input-calendar input" placeholder="{placeholder}" readonly>
                            {#if type === "datetime"}
                                <input tabindex="-1" type="hidden" name="to-calendar-time" class="to-calendar-time hidden-calendar-input is-hovered {inputClasses} input-calendar input" placeholder="{placeholder}" readonly>
                            {/if} 
                        </div>
                    {/if}
                </div>  
            </div>            
            <div class="field has-addons container-visible-inputs is-range-{isRange}" style="{visivility}">
                <div class="control">                    
                    <button type="button" tabindex="-1" class="button is-primary is-rounded is-outlined button-icon has-background-white">
                        <span class="icon is-left">
                            <i class="icon-calendar-3pod"></i>
                        </span>
                    </button>
                </div>
                <input tabindex="-1" type="hidden" class="input-total-result {classes}" name="{nameInputResult}" bind:this="{inputResult}">
                <div class="control">
                    {#if isRange === false}
                        <span class="icon icon-delete" on:click="{restoreAll}" >
                            <div class="delete has-background-danger"></div>
                        </span>
                    {/if}
                    <input tabindex="-1" type="text" on:keydown|preventDefault="{(e)=>{}}" class="results-calendar-from results-calendar-input is-hovered is-rounded {inputClasses} input-calendar input" placeholder="{placeholder}">
                </div>
                {#if isRange === true}
                    <div class="control">
                        <input tabindex="-1" type="text" on:keydown|preventDefault="{(e)=>{}}" class="results-calendar-to results-calendar-input is-hovered is-rounded {inputClasses} input-calendar input" placeholder="{placeholder}">
                    </div>
                {/if}
            </div>
        </div>        
    </div>
    <div class="container-pickadate pickadate-Datepicker" tabindex="-1"></div>
    <div class="container-pickadate pickadate-Timepicker" tabindex="-1"></div>
</div>